import React, { useContext } from "react";
import Keycloak, { KeycloakInstance } from "keycloak-js";
import { adminRealmRole } from "./common";

interface IKeycloakProps {
  /**
   * Initiated Keycloak instance
   */
  keycloakInstance: KeycloakInstance;
  /**
   * Current keycloak token
   */
  getCurrentToken: () => string;
  /**
   * Logged=in user ID
   */
  userId: string;
  /**
   * If logged-in user is an FA ADMIN.
   */
  isAdmin: boolean;
  /**
   * Check if current user has roles (array) in the current client. Returns true if at least one role is present.
   */
  hasClientRoles: (roles: string[]) => boolean;
}

export const KeycloakContext = React.createContext<KeycloakInstance>(
  Keycloak()
);

/**
 * Get keycloak initiated instance among other properties.
 * - keycloakInstance
 * - getCurrentToken()
 * - isAdmin
 * - userId
 * - hasClientRoles(roles)
 */
export const useKeycloak = (): IKeycloakProps => {
  const keycloakInstance = useContext(KeycloakContext);
  return {
    keycloakInstance,
    getCurrentToken: () => keycloakInstance.token || "",
    isAdmin: keycloakInstance.hasRealmRole(adminRealmRole),
    userId: keycloakInstance.subject || "",
    hasClientRoles: (roles) =>
      roles.some((role) => keycloakInstance.hasResourceRole(role)),
  };
};
