import { useState, useEffect } from "react";
import {
  // faLightbulbOn,
  // faLightbulbSlash,
  faSun,
  faMoon,
} from "@faharmony/icons";
import { setTheme, getCurrentDarkModePreference } from "@faharmony/theme";
// useKeyPress,
// keyPressCombos,

export default () => {
  const [darkMode, setDarkMode] = useState(getCurrentDarkModePreference);
  useEffect(() => {
    setTheme({ enableDarkMode: darkMode });
  }, [darkMode]);
  // const isKeyPressed = useKeyPress(keyPressCombos.switchDarkMode);
  // useEffect(() => {
  //   if (isKeyPressed) setDarkMode((p) => !p);
  // }, [isKeyPressed]);
  return {
    id: "darkMode",
    // icon: darkMode ? faLightbulbOn : faLightbulbSlash,
    icon: darkMode ? faSun : faMoon,
    // tooltip: `Switch to ${
    //   darkMode ? "light" : "dark"
    // } mode [${getKeyPressComboString(keyPressCombos.switchDarkMode)}]`,
    tooltip: `Switch to ${darkMode ? "light" : "dark"} mode`,
    onClick: () => setDarkMode((p) => !p),
  };
};
