import React from "react";
import { faChevronDown, faChevronRight } from "@faharmony/icons";
// import { motion, AnimatePresence } from "@faharmony/helpers";
import { Theme } from "@faharmony/theme";
// import { useDisableAnimation } from "@faharmony/helpers";
import { Text } from "../Text";
import { Box } from "../Box";
import { Grid } from "../Grid";
import { Icon } from "../Icon";
import {
  IAccordionOptions,
  IAccordionPanelProps,
  IAccordionProps,
} from "./types";

const PanelHeader = (
  props: IAccordionPanelProps &
    IAccordionOptions & {
      isExpanded: boolean;
    }
) => {
  const {
    isExpanded,
    handleExpand,
    handleCollapse,
    heading,
    id,
    action,
    disabled,
  } = props;
  return (
    <header
      style={{
        position: "sticky",
        top: 0,
        zIndex: 1,
        width: "100%",
        minHeight: `calc(3.5 * ${Theme.spacing()})`,
        height: "auto",
        padding: `${Theme.spacing("sm")} ${Theme.spacing()}`,
        borderRadius: Theme.radius("sm"),
        cursor: disabled ? "initial" : "pointer",
        background: Theme.colorBackground("rowAlt"),

        // background: isExpanded
        //   ? Theme.colorBackground("rowAlt")
        //   : "transparent",
      }}
      onClick={
        disabled
          ? undefined
          : () => (isExpanded ? handleCollapse(id) : handleExpand(id))
      }
    >
      <Grid gridTemplate=" auto / auto max-content">
        <Box justifyContent="flex-start">
          <Text
            value={heading}
            variant="strong"
            // color={isExpanded ? "primary" : "secondary"}
          />
        </Box>
        {(action || !disabled) && (
          <Box justifyContent="flex-end" spacing="md">
            {action && <div>{action}</div>}
            {!disabled && (
              <Icon icon={isExpanded ? faChevronDown : faChevronRight} />
            )}
          </Box>
        )}
      </Grid>
    </header>
  );
};

