export const Panel = (
  props: IAccordionPanelProps &
    IAccordionOptions & {
      fullHeight?: IAccordionProps["fullHeight"];
    }
) => {
  const { expandedIds, id, content, fullWidth, fullHeight = false } = props;
  const isExpanded = expandedIds.indexOf(id) > -1;
  // const disableAnimation = useDisableAnimation();
  return (
    <Box
      id={"accordion-" + id}
      height={isExpanded && fullHeight ? "100%" : "auto"}
      direction="column"
      alignItems="flex-start"
      className="accordion-panel"
      spacing="none"
    >
      <PanelHeader {...props} {...{ isExpanded }} />
      {/* {disableAnimation ? ( */}
      {isExpanded ? (
        <div style={{ padding: fullWidth ? 0 : Theme.spacing() }}>
          {content}
        </div>
      ) : null}
      {/* ) : ( */}
      {/* <AnimatedWrapper {...props} {...{ isExpanded }} /> */}
      {/* )} */}
    </Box>
  );
};
