import React, { useRef } from "react";
import { styled, Theme } from "@faharmony/theme";
import {
  faAngleDoubleLeft,
  faAngleLeft,
  faAngleRight,
  faAngleDoubleRight,
} from "@faharmony/icons";
import { Text, Box, Button } from "@faharmony/components";
import { useElementDimensions } from "@faharmony/helpers";
import { TableChildrenProps } from "../types";

const calcShowingRows = (
  pageSize: number,
  pageIndex: number,
  totalRows: number
) => {
  const start = pageIndex * pageSize + 1;
  const end = pageIndex * pageSize + pageSize;
  return `${start} - ${end > totalRows ? totalRows : end}`;
};

export const TablePagination = <T extends object>({
  tableInstance,
  tableProps,
}: TableChildrenProps<T>) => {
  const {
    canPreviousPage,
    canNextPage,
    pageOptions,
    pageCount,
    gotoPage,
    nextPage,
    previousPage,
    rows,
    state,
  } = tableInstance;
  const { pageIndex, pageSize } = state;

  const { isLoading } = tableProps;

  const targetRef = useRef<HTMLDivElement>(null);
  const { width: elWidth } = useElementDimensions(targetRef);
  const wideEnough = elWidth > 550;

  const StyledPagination = styled.div`
    display: ${wideEnough ? "grid" : "block"};
    background-color: ${() => Theme.colorBackground("base")};
    grid-template-columns: auto max-content;
    justify-content: space-between;

    input,
    select {
      font-family: inherit;
      font-weight: bold;
      font-size: 0.9rem;
      color: ${() => Theme.colorText("primary")};
      background-color: ${() => Theme.colorBackground("elevated")};
      border-radius: ${() => Theme.radius("sm")};
      height: 28px;
      border: 1px solid ${() => Theme.colorOutline("border")};
      padding: 0 4px;
      text-align: center;
    }
  `;

  return (
    <StyledPagination className="pagination" ref={targetRef}>
      {wideEnough && (
        <Box style={{ paddingTop: Theme.spacing("sm") }}>
          {isLoading ? (
            <Text value={`Loading...`} color="disabled" />
          ) : rows.length > 0 ? (
            <Text
              value={`Showing ${calcShowingRows(
                pageSize,
                pageIndex,
                rows.length
              )} of ${rows.length} entries`}
              color="secondary"
            />
          ) : (
            <Text value={`There are 0 entries`} color="disabled" />
          )}
        </Box>
      )}
      <Box justifyContent={wideEnough ? "flex-end" : "space-around"}>
        <Box justifyContent="flex-start">
          <Button
            icon={faAngleDoubleLeft}
            onClick={() => gotoPage(0)}
            disabled={!canPreviousPage}
            spacing="sm"
            outline
          />
          <Button
            icon={faAngleLeft}
            onClick={() => previousPage()}
            disabled={!canPreviousPage}
            spacing="sm"
            outline
          />
        </Box>
        <Box
          justifyContent="center"
          style={{ paddingTop: Theme.spacing("sm") }}
          spacing="sm"
        >
          <Text value={"Page"} />
          <div style={{ marginTop: "-4px" }}>
            <input
              type="number"
              min={1}
              disabled={!canNextPage && !canPreviousPage}
              placeholder={"Page"}
              step={1}
              value={pageIndex + 1}
              max={pageOptions.length}
              onChange={(e) => {
                const page = e.target.value ? Number(e.target.value) - 1 : 0;
                gotoPage(page);
              }}
              style={{ width: "48px", maxWidth: "100px" }}
            />
          </div>
          <Text value={"of"} />
          <Text value={`${pageOptions.length}`} variant="h5" />
        </Box>
        <Box justifyContent="flex-end">
          <Button
            icon={faAngleRight}
            onClick={() => nextPage()}
            disabled={!canNextPage}
            spacing="sm"
            outline
          />
          <Button
            icon={faAngleDoubleRight}
            onClick={() => gotoPage(pageCount - 1)}
            disabled={!canNextPage}
            spacing="sm"
            outline
          />
        </Box>
      </Box>
    </StyledPagination>
  );
};

export default TablePagination;
