import { useState, useEffect, useCallback } from "react";

/**
 * Hook can be used to indicate to the users the status of any async request
 * @author Gabe Ragland <> https://github.com/gragland
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @param asyncFunction A function that returns a Promise
 * @param immediate True if execute immediately. False to wait till "execute()" is called.
 * @example
 *  const { execute, pending, value, error } = useAsync(fetch(apiURL), false);
 */
export const useAsync = (
  asyncFunction: () => Promise<any>,
  immediate = true
) => {
  const [pending, setPending] = useState(false);
  const [value, setValue] = useState<any>(null);
  const [error, setError] = useState<any>(null);

  // The execute function wraps asyncFunction and
  // handles setting state for pending, value, and error.
  // useCallback ensures the below useEffect is not called
  // on every render, but only if asyncFunction changes.
  const execute = useCallback(() => {
    setPending(true);
    setValue(null);
    setError(null);
    return asyncFunction()
      .then(setValue)
      .catch(setError)
      .finally(() => setPending(false));
  }, [asyncFunction]);

  // Call execute if we want to fire it right away.
  // Otherwise execute can be called later, such as
  // in an onClick handler.
  useEffect(() => {
    if (immediate) {
      execute();
    }
  }, [execute, immediate]);

  return { execute, pending, value, error };
};
