import React, { useContext } from "react";
import { IAppConfig } from "../types";

interface IAppProps {
  /** Provided App configuration like name, icon, roles, etc. */
  appConfig: IAppConfig;
}

const AppContext = React.createContext<IAppConfig>({} as IAppConfig);

/**
 * Hook to get App information.
 * - appConfig
 */
const useApp = (): IAppProps => {
  const appConfig = useContext(AppContext);
  return { appConfig };
};

export { AppContext, useApp };
