import React from "react";
import {
  faTimes,
  faCheckCircle,
  faExclamationCircle,
  faInfoCircle,
  faTimesCircle,
} from "@faharmony/icons";
import { MotionProps } from "@faharmony/helpers";
import { useTimeoutConditionally } from "@faharmony/helpers";
import { Icon } from "../Icon";
import { ToastVariant, IToastProps } from "./types";
import { StyledToast } from "./style";
import { useToast } from "./context";
import { Theme } from "@faharmony/theme";

const Toast = (toast: IToastProps) => {
  const { removeToast } = useToast();
  const { id, title, description, variant, persist = false } = toast;
  useTimeoutConditionally(() => removeToast(id), 5000, !persist);

  const toastIcon = (variant: ToastVariant = "danger") => {
    if (variant === "success") return faCheckCircle;
    if (variant === "warning" || variant === "danger")
      return faExclamationCircle;
    if (variant === "info") return faInfoCircle;
    return faTimesCircle;
  };

  const motionProps: MotionProps = {
    initial: "initial",
    animate: "animate",
    exit: "exit",
    transition: Theme.transition(),
    layout: true,
    variants: {
      initial: { right: -400 },
      animate: { right: 0 },
      exit: { right: -400 },
    },
  };
  return (
    <StyledToast variant={variant} className={`toast`} {...motionProps}>
      <div className="toast-content">
        <div className="toast-icon">
          {variant && <Icon icon={toastIcon(variant)} size="1x" />}
        </div>
        <div className="toast-text">
          <p className="toast-title">{title}</p>
          {description && (
            <small className="toast-message">{description}</small>
          )}
        </div>
        <div className="toast-close" onClick={() => removeToast(id)}>
          <Icon icon={faTimes} size="1x" />
        </div>
      </div>
    </StyledToast>
  );
};

export default Toast;
