const StyledIcon = styled.span`
  display: inline-block;
  width: ${() => `calc(${Theme.spacing("xl")} + ${Theme.spacing("sm")})`};
  height: ${() => `calc(${Theme.spacing("xl")} + ${Theme.spacing("sm")})`};
  font-size: ${() => Theme.spacing("xl")};
  padding-bottom: ${() => Theme.spacing("xs")};
  text-align: center;
`;

/**
 * Icon
 * ---
 * Render an icon from FontAwesome library. By default, renders FA logo icon.
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @version 1.5.0
 * @example
 * <Icon name="faFolders" />
 * <Icon name="faSpinner" spin />
 */
const Icon = React.forwardRef<HTMLSpanElement, IIconProps>((props, ref) => {
  const {
    icon,
    size,
    rotate,
    flip,
    spin,
    pulse,
    color,
    onClick,
    className,
  } = props;
  const fontAwesomeIconProps: FontAwesomeIconProps = {
    icon: icon || FALogoIconDefinition,
    size,
    rotate,
    flip,
    spin,
    pulse,
    color,
    onClick,
    role: "icon",
    fixedWidth: size ? false : true,
  };
  const fontAwesomeIcon = (
    <FontAwesomeIcon {...fontAwesomeIconProps} className={"iconSVG"} />
  );

  if (size) {
    return fontAwesomeIcon;
  } else
    return (
      <StyledIcon ref={ref} className={(className || "") + " icon"}>
        {fontAwesomeIcon}
      </StyledIcon>
    );
});

export { Icon, IIconProps };
