import React from "react";
import { Theme, styled } from "@faharmony/theme";
import { faLayerGroup } from "@faharmony/icons";
import { Popover, Button, Text, Box } from "@faharmony/components";
import { IFABarProps } from "../types";
import FALogo from "./FALogo";
import { isAppActive } from "./common";

const barHeight = "48px";

const StyledBar = styled.header`
  position: relative;
  z-index: ${() => Theme.zIndex("docked")};
  margin: 0;
  box-shadow: ${() => Theme.shadow("large")};
  width: 100%;
  max-width: 100vw;
  height: ${barHeight};
  background-color: ${() => Theme.colorBackground("base")};
  display: grid;
  gap: ${() => Theme.spacing("sm")};
  grid-template-columns: 48px 1fr max-content;
  align-items: center;

  /* #FA-Bar-logo {
    transform: translateY(-4px);
  } */

  #FA-Bar-actions {
    padding: 0 ${() => Theme.spacing()};
  }
`;

const HorizontalBar = ({ actions, apps }: IFABarProps) => {
  const currentAppName: string =
    apps.filter(isAppActive)[0]?.appName.split("FA ")[1] || "Platform";

  return (
    <StyledBar>
      <FALogo horizontal />

      <div style={{ overflow: "hidden" }}>
        <Text value={currentAppName} truncate variant="h1" />
      </div>
      <Box
        id={"FA-Bar-actions"}
        direction={"row"}
        justifyContent="flex-end"
        alignItems="center"
      >
        <Popover.Menu
          placement="bottom"
          menuItems={apps.map((app) => {
            const { appName, url, icon, openInNewWindow, id } = app;
            const isActive = isAppActive(app);
            return {
              id: id,
              label: appName,
              icon,
              onClick: () =>
                window.open(url, openInNewWindow ? "_blank" : "_self"),
              color: isActive ? "accent" : undefined,
            };
          })}
        >
          <Button
            icon={faLayerGroup}
            tooltip="App switcher"
            variant="secondary"
            id="faGlobalBarAppSwitcher"
          />
        </Popover.Menu>
        {actions.map((action) => (
          <Button
            key={action.id}
            {...action}
            variant="secondary"
            id={"faGlobalBarAction-" + action.id}
          />
        ))}
      </Box>
    </StyledBar>
  );
};

export default HorizontalBar;
