import { styled, Theme } from "@faharmony/theme";
import { IMenuPrimaryItemProps, IMenuSecondaryItemProps } from "../types";

interface StyleProps {
  isMenuCollapsed: boolean;
  isHeaderShown: boolean;
}

export const StyledNavigationMenu = styled.nav<StyleProps>`
  position: relative;
  width: inherit;
  height: inherit;
  background: transparent;
  padding-top: ${() => Theme.spacing()};
  padding-bottom: ${() => Theme.spacing()};
  overflow: auto;
  ::-webkit-scrollbar,
  ::-webkit-scrollbar:horizontal,
  ::-webkit-scrollbar:vertical {
    width: ${Theme.spacing("xs")};
    height: ${Theme.spacing("xs")};
  }

  ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
  }

  .noContent {
    color: ${() => Theme.colorText("disabled")};
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
`;

const iconWidth = "24px";

const NavItemCommonStyle = (
  props: IMenuPrimaryItemProps | IMenuSecondaryItemProps
) => `     
  display: grid;
  grid-gap: ${Theme.spacing()};
  padding: 0 ${Theme.spacing()};
  align-items: center;
  background: ${
    props.isSelected ? Theme.colorBackground("selected") : "transparent"
  };
  color: ${
    props.isSelected ? Theme.colorText("primary") : Theme.colorText("secondary")
  };
  grid-template-columns: ${
    props.showLeadingIcon ? `${iconWidth} auto max-content` : "auto max-content"
  };
  cursor: pointer;
  border-left: 2px solid ${
    props.isSelected ? Theme.colorTint("accent") : "transparent"
  };

  .Text {
    cursor: pointer;
    user-select: none;
  }
`;

const IconWrapperStyle = () => `.iconWrapper {
  width: ${iconWidth};
  .Icon {
    margin-top: calc(-1 * ${() => Theme.spacing("xs")});
  }
}`;

const HoverStyle = (props: IMenuPrimaryItemProps | IMenuSecondaryItemProps) => `
  :hover {
    background: ${
      props.isSelected
        ? Theme.colorBackground("selected")
        : Theme.colorBackground("hover")
    };
    color: ${Theme.colorText("primary")};
  }
`;

export const StyledNavPrimaryItem = styled.li<IMenuPrimaryItemProps>`
  min-height: 40px;
  margin-bottom: ${() => Theme.spacing()};
  background: ${(props) =>
    props.isExpanded ? Theme.colorBackground("base") : "transparent"};
  padding-bottom: ${(props) => (props.isExpanded ? Theme.spacing("sm") : 0)};

  .navPrimaryItem {
    min-height: 40px;
    ${NavItemCommonStyle};
    ${IconWrapperStyle};
    ${HoverStyle};
  }
`;

export const StyledNavSecondaryItem = styled.li<IMenuSecondaryItemProps>`
  min-height: 32px;
  ${NavItemCommonStyle};
  ${IconWrapperStyle};
  ${HoverStyle};
`;

export default StyledNavigationMenu;
