import axios, { AxiosRequestConfig } from "axios";
import { useAxios } from "./useAxios";

export const faLegacyApiInstance = axios.create({
  baseURL: `/rs/secure/fa/api/`,
});
faLegacyApiInstance.interceptors.request.use(
  (config) => {
    // config.headers.Authorization = `Bearer ${getAuthToken()}`;
    config.headers.Accept = "application/json";
    return config;
  },
  (error) => Promise.reject(error)
);

/**
 * useFaApi
 * ---
 * Use FA's main api available at `/rs/` endpoint.
 * @param  options
 * @param  lazy - true: to prevent execution onMount, default: false
 * @param  version - Version of API to use, default: 1.0
 * @example
 * const [state, execute, cancel] = useFaApi<DataType>({url: "contact", method: "POST"}, {tokenName: ''; tokenValue: ''}, false, "1.1");
 * const { isLoading, isCancelled, error, success, data} = state;
 */
export const useFaLegacyApi = <T = any>(
  options: AxiosRequestConfig,
  credentials: { tokenName: string; tokenValue: string },
  lazy: boolean = false,
  version = "1.0"
) =>
  useAxios<T>(
    {
      ...options,
      url: `v${version}/${options.url}`,
      headers: {
        ...options.headers,
        "fa-token-value": credentials.tokenValue,
        "fa-token-name": credentials.tokenName,
      },
    },
    faLegacyApiInstance,
    lazy
  );
