import React, { forwardRef } from "react";
import { preventDefaultOnClick } from "@faharmony/helpers";
import { Text } from "../Text";
import { Icon } from "../Icon";
import { Tooltip } from "../Tooltip";
import { Popover } from "../Popover";
import { IButtonProps, IStyleProps } from "./types";
import { StyledButton } from "./style";
import { faSpinner } from "@faharmony/icons";

/**
 * Button
 * ---
 * A clickable button with multiple states, sizes and style variants.
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @version 1.5.0
 * @example
 * <Button value="button label" icon={faBell} />
 */
const Button = forwardRef<HTMLButtonElement, IButtonProps>((props, ref) => {
  const {
    id,
    className,
    value,
    icon,
    children,
    tooltip,
    onClick = preventDefaultOnClick,
    onContextMenu = preventDefaultOnClick,
    spacing = "md",
    disabled = false,
    fullWidth = false,
    variant = "default",
    outline: isOutline = false,
    loading: isLoading = false,
    loadingText,
    popoverConfirmOptions,
    popoverContentCallback,
    popoverMenuItems,
    type,
  } = props;
  const styleProps: IStyleProps = {
    disabled,
    fullWidth,
    isLoading,
    spacing,
    isOutline,
    variant,
  };
  const text = value || children || tooltip || "";

  let button = (
    <StyledButton
      {...styleProps}
      id={id}
      ref={ref}
      role="button"
      className={"button " + className}
      name={text}
      onClick={disabled || isLoading ? undefined : onClick}
      onContextMenu={onContextMenu}
      onBlur={() => {}}
      type={type || "button"}
    >
      {(icon || isLoading) && (
        <Icon icon={isLoading ? faSpinner : icon} spin={isLoading} />
      )}
      {(value || children) && (
        <Text
          value={isLoading && loadingText ? `${loadingText}...` : text}
          variant={variant === "primary" ? "h5" : "p"}
          truncate
        />
      )}
    </StyledButton>
  );

  let popoverFlag = false;

  if (value || children || icon) {
    if (popoverContentCallback) {
      popoverFlag = true;
      button = <Popover content={popoverContentCallback}>{button}</Popover>;
    }
    if (popoverMenuItems) {
      popoverFlag = true;
      button = (
        <Popover.Menu menuItems={popoverMenuItems}>{button}</Popover.Menu>
      );
    }
    if (popoverConfirmOptions) {
      popoverFlag = true;
      button = (
        <Popover.Confirm {...popoverConfirmOptions}>{button}</Popover.Confirm>
      );
    }
    if (tooltip)
      if (popoverFlag)
        button = (
          <Tooltip value={tooltip} disabled={disabled}>
            <span>{button}</span>
          </Tooltip>
        );
      else
        button = (
          <Tooltip value={tooltip} disabled={disabled}>
            {button}
          </Tooltip>
        );

    return button;
  }

  // WARNING: Button is empty.
  console.warn(
    `Button could not be rendered since neither icon or text (value/children) were provided.`
  );
  return null;
});

export default Button;
