import React, { useState, useEffect, useCallback } from "react";
import { Box } from "../Box";
import {
  IAccordionOptions,
  IAccordionPanelProps,
  IAccordionProps,
} from "./types";
import { Panel } from "./Panel";

const useAccordion = (props: IAccordionProps): IAccordionOptions => {
  const [expandedIds, setExpandedIds] = useState<string[]>([]);
  useEffect(() => {
    const { independent = false, panels = [] } = props;
    const activePanelIds = panels
      .filter((panel) => (panel.defaultActive ? true : false))
      .map((panel) => panel.id);
    setExpandedIds(
      independent || activePanelIds.length <= 1
        ? activePanelIds
        : [activePanelIds[0]]
    );
  }, [props]);

  const handleExpand = useCallback(
    (panelId: string) => {
      if (props.independent) {
        setExpandedIds((prevIds) =>
          prevIds.indexOf(panelId) > -1 ? prevIds : [...prevIds, panelId]
        );
      } else setExpandedIds([panelId]);
    },
    [props.independent]
  );
  const handleCollapse = useCallback((panelId: string) => {
    setExpandedIds((prevIds) => prevIds.filter((id) => id !== panelId));
  }, []);

  return { expandedIds, handleExpand, handleCollapse };
};

/**
 * Accordion
 * ---
 * A set of collapsible panels set in vertical orientation.
 * Can be used for displaying multiple set of information.
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @version 1.5.0
 * @example
 * <Accordion
 *  panels={[
 *    {
 *      id: "panel1",
 *      heading: "Panel heading",
 *      content: <Panel1 />
 *    },
 *  ]}
 * />
 */
const Accordion = (props: IAccordionProps) => {
  const accordionOptions = useAccordion(props);

  return (
    <Box
      direction="column"
      spacing="md"
      height={props.fullHeight ? "100%" : "auto"}
      className="accordion"
    >
      {props.panels.map((panel) => (
        <Panel
          key={panel.id}
          {...panel}
          {...accordionOptions}
          fullHeight={props.fullHeight}
        />
      ))}
    </Box>
  );
};

export { Accordion, IAccordionProps, IAccordionPanelProps };
