import React from "react";
import { Text, Icon } from "@faharmony/components";
import { OnClickEventType } from "@faharmony/helpers";
import { IMenuSecondaryItemProps } from "../types";
import { StyledNavSecondaryItem } from "./Style";

const commonId = "navSecondaryItem";

const NavSecondaryItem = React.memo((props: IMenuSecondaryItemProps) => {
  const { item, showLeadingIcon } = props;
  const { label, icon, trailingIcon, trailingIconTint, id } = item;
  const handleClick = (e: OnClickEventType) => {
    e?.preventDefault();
    e?.stopPropagation();
    item.handleClick && item.handleClick(e);
    props.handleItemSelect(item);
  };

  return (
    <StyledNavSecondaryItem
      {...props}
      onClick={handleClick}
      id={`${commonId}-${id}`}
    >
      {showLeadingIcon && (
        <div className="iconWrapper">
          {icon && <Icon icon={icon} color={"disabled"} />}
        </div>
      )}
      <Text value={label} />
      {trailingIcon && (
        <div className="iconWrapper">
          <Icon icon={trailingIcon} color={trailingIconTint} />
        </div>
      )}
    </StyledNavSecondaryItem>
  );
});

NavSecondaryItem.displayName = "NavSecondaryItem";
export { NavSecondaryItem };
export default NavSecondaryItem;
