/**
 * Get date-time string in FA format
 * @param date Unix time in seconds (number)
 * @param format One of "dateTime" | "date" | "time"
 * @returns YYYY-MM-DD  HH:mm:ss
 */
export const readableDateTimeFormat = (
  date: Date,
  format: "dateTime" | "date" | "time" = "dateTime"
) => {
  const padStart = (num: number, pad = 2): string =>
    num.toString().padStart(pad, "0");
  const readableDate = (date: Date) => {
    const YYYY = padStart(date.getFullYear(), 4);
    const MM = padStart(date.getMonth() + 1);
    const DD = padStart(date.getDate());
    return `${YYYY}-${MM}-${DD}`;
  };
  const readableTime = (date: Date) => {
    const HH = padStart(date.getHours());
    const mm = padStart(date.getMinutes());
    const ss = padStart(date.getSeconds());
    return `${HH}:${mm}:${ss}`;
  };
  switch (format) {
    case "date":
      return readableDate(date);
    case "time":
      return readableTime(date);
    default:
      return `${readableDate(date)} ${readableTime(date)}`;
  }
};
