import { useState } from "react";
import {
  i18nProps,
  languageNativeNames,
  LanguageCode,
  getCurrentLanguageCode,
  setCurrentLanguageCode,
  getLocaleLanguageCodes,
} from "@faharmony/locale";
import { faGlobe } from "@faharmony/icons";
import { IPopoverMenuItemProps } from "@faharmony/components";
import { IFABarProps } from "../types";

export default (i18n?: i18nProps): IFABarProps["actions"] => {
  const [i18nextLng, setI18nextLng] = useState<LanguageCode>(
    getCurrentLanguageCode() || i18n?.language || "en"
  );
  if (i18n && getLocaleLanguageCodes(i18n).length > 1) {
    const menuItems = getLocaleLanguageCodes(i18n).map(
      (code): IPopoverMenuItemProps => ({
        id: code,
        label: languageNativeNames[code]?.label || "English",
        disabled: code === i18nextLng,
        onClick: () => {
          i18n && i18n.changeLanguage(code);
          setCurrentLanguageCode(code);
          setI18nextLng(code);
        },
      })
    );
    const tooltip =
      languageNativeNames[i18nextLng]?.changeLabel || "Change language";
    return [
      {
        id: "language",
        icon: faGlobe,
        tooltip,
        popoverMenuItems: menuItems,
      },
    ];
  }
  return [];
};
