import React from "react";
import { faTimesCircle } from "@faharmony/icons";
import { OnClickEventType } from "@faharmony/helpers";
import {
  Grid,
  Text,
  IIconButtonObject,
  Button,
  Box,
  Toolbar,
  Divider,
} from "@faharmony/components";
import { Theme } from "@faharmony/theme";
import { StyledBody, StyledFooter, StyledHead, StyledView } from "./style";

interface ISummaryViewProps {
  /**
   * Content of the View
   * @example
   * <SummaryView {...props}> {children} </SummaryView>
   */
  children?: React.ReactNode;
  /**
   * Title/heading of the view
   */
  heading: string;
  /**
   * Appears above the Heading.
   */
  caption: string;
  /**
   * Callback to close the summary view. A close button is rendered in the top-right corner.
   */
  onClose: (e?: OnClickEventType) => void;
  /**
   * The actions component is placed in the top right area.
   */
  actions?: IIconButtonObject[];
  /**
   * By default actions are placed in a spacial area below heading. If actionsInHeader={true}, the actions are placed in the top right area along side the close button. Suitable when view is placed in modal with wider width.
   * @default false
   */
  actionsInHeader?: boolean;
  /**
   * The content component is placed in the footer. It can be used to display any sort of stuff which is common for the view.
   */
  footerContent?: React.ReactNode;
}

/**
 * SummaryView
 * ---
 * Summary View is the secondary (temporary) view which can be used to display information.
 *
 * It can be used as base for Modal.
 *
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @version 1.5.0
 * @example
 * <SummaryView heading="Heading" caption="type"> {content} </SummaryView>
 */
const SummaryView = (props: ISummaryViewProps) => {
  const {
    children,
    heading,
    caption,
    footerContent,
    onClose,
    actions = [],
    actionsInHeader,
  } = props;

  const closeButtonObject: IIconButtonObject = {
    id: "close-summary-view",
    onClick: onClose,
    icon: faTimesCircle,
    tooltip: "Close",
  };

  let headerActions: IIconButtonObject[] = [];
  if (actionsInHeader) headerActions = [...actions] as IIconButtonObject[];
  headerActions = [...headerActions, closeButtonObject];

  return (
    <StyledView
      className="summaryView"
      hasActionArea={!actionsInHeader && !!actions.length}
      hasFooter={!!footerContent}
    >
      <StyledHead id="summaryView-header">
        <Grid gridTemplate="auto / auto max-content" overflow="inherit">
          <div id="summaryView-header-title">
            <Text
              value={caption.toString()}
              variant="strong"
              color="secondary"
              truncate
            />
            <Text value={heading} variant="h3" truncate />
          </div>
          <Box
            height="100%"
            alignItems="unset"
            style={{ paddingTop: Theme.spacing() }}
          >
            {headerActions.map((action) => (
              <Button key={action.id} {...action} spacing="sm" outline />
            ))}
          </Box>
        </Grid>
        {/* <div className="headerDivider">
          <Divider />
        </div> */}
      </StyledHead>
      {!actionsInHeader && !!actions.length && <Toolbar actions={actions} />}
      <StyledBody id="summaryView-body">
        {children ? (
          children
        ) : (
          <div id="summaryView-noContent">
            <Text value="No content available." color="secondary" />
          </div>
        )}
      </StyledBody>
      {footerContent && (
        <StyledFooter>
          <Divider />
          <div id="summaryView-footer-content">{footerContent}</div>
        </StyledFooter>
      )}
    </StyledView>
  );
};

export { SummaryView, ISummaryViewProps };
export default SummaryView;
