import React from "react";
import { Helmet } from "react-helmet";
import { Switch, Route, useRouteMatch, Redirect } from "@faharmony/router";
import { IModuleProps, ModuleProvider } from "@faharmony/module";
import LandingPage from "./LandingPage";
import { IRenderProps } from "../types";
import { useCheckUserForRolesCallback } from "./helpers";

const ModuleRouter = (Module: IModuleProps) => {
  const { path } = useRouteMatch();
  const { label, subModules = [] } = Module;
  return (
    <ModuleProvider Module={Module}>
      <Helmet>
        <title>{label}</title>
      </Helmet>
      <Switch>
        {subModules.map((subModule) => (
          <Route key={subModule.id} path={path + "/" + subModule.id}>
            <Helmet>
              <title>{label + " - " + subModule.label}</title>
            </Helmet>
            <subModule.component />
          </Route>
        ))}
        <Route exact path={path}>
          <Module.component />
        </Route>

        <Route path={path}>
          <Redirect to={path} />
        </Route>
      </Switch>
    </ModuleProvider>
  );
};

export const MainView = ({ Modules, appConfig }: IRenderProps) => {
  const checkRolesForUser = useCheckUserForRolesCallback();
  return (
    <React.Fragment>
      <Helmet
        key="helmetApp"
        titleTemplate={"FA " + appConfig.name + " - %s"}
        defaultTitle={"FA " + appConfig.name}
      >
        <meta
          name="description"
          content={appConfig.description || appConfig.name}
        />
      </Helmet>

      <Switch key="switch">
        {/* Modules */}
        {Modules.map((Module) =>
          checkRolesForUser(Module.roles) ? (
            <Route path={`/${Module.id}`} key={Module.id}>
              <ModuleRouter {...Module} />
            </Route>
          ) : null
        )}
        {/* Default */}
        <Route path="/">
          <LandingPage appConfig={appConfig} Modules={Modules} />
        </Route>
      </Switch>
    </React.Fragment>
  );
};

export default MainView;
