import React from "react";
import { Theme, styled } from "@faharmony/theme";
import {
  Box,
  Icon,
  Tooltip,
  Button,
  Popover,
  IPopoverMenuItemProps,
} from "@faharmony/components";
import { faExternalLinkSquare } from "@faharmony/icons";
import { IFABarProps, IFABarAppButtonProps } from "../types";
import FALogo from "./FALogo";

const barWidth = "52px";
const appButtonHeight = "36px";

const StyledBar = styled.aside`
  position: relative;
  z-index: ${() => Theme.zIndex("docked")};
  margin: 0;
  box-shadow: ${() => Theme.shadow("large")};
  width: ${barWidth};
  max-width: ${barWidth};
  height: 100%;
  max-height: 100vh;
  background-color: ${() => Theme.colorBackground("base")};
  display: grid;
  grid-template-rows: ${barWidth} minmax(auto, 1fr) max-content;
  gap: ${() => Theme.spacing("sm")};

  #FA-Bar-apps {
    margin-top: 6px;
  }

  #FA-Bar-actions {
    padding-bottom: ${() => Theme.spacing()};
  }
`;

const StyledAppButton = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-size: 1.75rem;
  cursor: pointer;
`;

const StyledAppButtonInactive = styled(StyledAppButton)`
  height: ${appButtonHeight};
  width: ${appButtonHeight};
  box-shadow: ${() => Theme.shadow("outline")};
  border-radius: ${() => Theme.radius("sm")};
  background: ${() => Theme.colorBackground("elevated")};
  color: ${() => Theme.colorText("secondary")};
  :hover {
    color: ${() => Theme.colorText("primary")};
    box-shadow: ${() => Theme.shadow()};
  }
`;

const StyledAppButtonActive = styled(StyledAppButton)`
  height: calc(${appButtonHeight} + ${() => Theme.spacing()});
  width: calc(${barWidth} - ${() => Theme.spacing("xs")});
  box-shadow: ${() => Theme.shadow("inner")};
  border-radius: ${() => `${Theme.radius()} 0 0 ${Theme.radius()}`};
  background: ${() => Theme.colorBackground("disabled")};
  margin-left: ${() => Theme.spacing("xs")};
  padding-right: ${() => Theme.spacing("xs")};
  color: ${() => Theme.colorTint()};
  :hover {
    color: ${() => Theme.colorTint()};
    box-shadow: ${() => Theme.shadow("inner")};
  }
`;

const AppButton = (props: IFABarAppButtonProps) => {
  const { appName, icon, url, openInNewWindow, id } = props;
  const urlWithoutTrailingSlash = url.endsWith("/")
    ? url.slice(0, url.length - 1)
    : url;
  const isActive = window.location.pathname.startsWith(urlWithoutTrailingSlash);
  const target = openInNewWindow ? "_blank" : "_self";
  const ButtonWrapper = isActive
    ? StyledAppButtonActive
    : StyledAppButtonInactive;
  const appButtonMenuItems: IPopoverMenuItemProps[] = [
    {
      id: "openSameWindow",
      label: "Open app",
      onClick: () => window.open(url, "_self"),
    },
    {
      id: "openNewWindow",
      label: "Open app in new tab",
      onClick: () => window.open(url, "_blank"),
      icon: faExternalLinkSquare,
    },
  ];
  return (
    <Popover.Menu menuItems={appButtonMenuItems} trigger="contextmenu">
      <span tabIndex={0}>
        <Tooltip value={appName} placement="right">
          <ButtonWrapper
            onClick={() => window.open(url, target)}
            id={"faGlobalBarApp-" + id}
          >
            <Icon icon={icon} size="1x" />
          </ButtonWrapper>
        </Tooltip>
      </span>
    </Popover.Menu>
  );
};

const VerticalBar = ({ actions, apps }: IFABarProps) => {
  return (
    <StyledBar id="faGlobalBar">
      <FALogo />
      <Box
        id={"FA-Bar-apps"}
        direction={"column"}
        justifyContent="flex-start"
        alignItems="center"
        style={{ overflowX: "hidden", overflowY: "auto" }}
      >
        {apps.map((app) => (
          <AppButton key={app.id} {...app} />
        ))}
      </Box>
      <Box
        id={"FA-Bar-actions"}
        direction={"column"}
        justifyContent="flex-end"
        alignItems="center"
      >
        {actions.map((action) => (
          <Button
            key={action.id}
            {...action}
            variant="secondary"
            id={"faGlobalBarAction-" + action.id}
          />
        ))}
      </Box>
    </StyledBar>
  );
};

export default VerticalBar;
