import { useKeycloak } from "@faharmony/service";
import { IModuleProps } from "@faharmony/module";

// If admin, show the module
// If there are no roles mentioned, show the module.
// If roles are present, then only show to those who have roles

export const useCheckUserForRoles = (roles?: string[]) => {
  const { isAdmin, hasClientRoles } = useKeycloak();
  return (
    isAdmin || (roles ? (!!roles.length ? hasClientRoles(roles) : true) : true)
  );
};

export const useCheckUserForRolesCallback = () => {
  const { isAdmin, hasClientRoles } = useKeycloak();
  return (roles?: string[]) =>
    isAdmin || (roles ? (!!roles.length ? hasClientRoles(roles) : true) : true);
};

export const combineModuleRoles = (Modules: IModuleProps[]) => {
  let allRoles: string[] = [];
  Modules.forEach((Module) => {
    const mRoles = Module.roles ? Module.roles : [];
    allRoles = [...allRoles, ...mRoles];
  });
  allRoles = allRoles.filter((item, pos) => allRoles.indexOf(item) === pos);
  return allRoles;
};
