import React from "react";
import { Theme, styled } from "@faharmony/theme";
import { useDisableAnimation } from "@faharmony/helpers";
import { Divider } from "@faharmony/components";
import {
  IMenuProps,
  StyleProps,
  IMenuPrimaryItem,
  IMenuSecondaryItem,
} from "./types";
import {
  useMenuCollapse,
  useFilterNavigationItems,
  useCleanItems,
  calcGridTemplateRows,
} from "./helpers";
import Header from "./Header";
import Footer from "./Footer";
import Nav from "./nav";
import Actions from "./Actions";

const maxWidth = "248px";
const minWidth = "48px";

const StyledNavigation = styled.nav<StyleProps>`
  padding-top: ${(p) => (p.isMenuCollapsed ? Theme.spacing() : 0)};
  width: ${(p) => (p.isMenuCollapsed ? minWidth : maxWidth)};
  min-width: ${minWidth};
  max-width: ${(p) => (p.isMenuCollapsed ? minWidth : maxWidth)};
  height: 100%;
  min-height: ${maxWidth};
  max-height: 100vh;
  position: relative;
  z-index: 0;
  overflow: visible;
  background-color: ${() => Theme.colorBackground("disabled")};
  transition: ${(p) =>
    p.disableAnimation ? "" : `width 300ms, max-width 300ms`};
  display: grid;
  grid-template-rows: ${calcGridTemplateRows};
`;

/**
 * Navigation
 * ---
 *
 * Navigation component for FA applications.
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 *
 * @example
 *
 */
const Menu = ({
  isHeaderVisible = true,
  isMenuCollapsedDefault,
  actions = [],
  heading = "",
  shortHeading,
  descriptionText,
  menu = [],
  enableMenuFilter,
}: IMenuProps) => {
  actions = actions.slice(0, 5);
  const menuCollapseProps = useMenuCollapse(isMenuCollapsedDefault);
  const { isMenuCollapsed } = menuCollapseProps;
  const items = useCleanItems(menu);
  const { handleFiltering, filteredItems } = useFilterNavigationItems(items);

  return (
    <StyledNavigation
      isHeaderVisible={isHeaderVisible}
      isMenuCollapsed={isMenuCollapsed}
      actionsPresent={actions.length > 0}
      disableAnimation={useDisableAnimation()}
    >
      {isHeaderVisible && (
        <Header
          heading={heading}
          shortHeading={shortHeading}
          isMenuCollapsed={isMenuCollapsed}
        />
      )}
      {actions.length > 0 && (
        <Actions
          actions={actions}
          isMenuCollapsed={isMenuCollapsed}
          heading={heading}
          shortHeading={shortHeading}
        />
      )}
      {isMenuCollapsed && (
        <div style={{ margin: "8px 8px 0" }}>
          <Divider spacing="none" />
        </div>
      )}
      <Nav
        navItems={filteredItems}
        isMenuCollapsed={isMenuCollapsed}
        isHeaderShown={isHeaderVisible && actions.length > 0}
      />
      <Footer
        {...menuCollapseProps}
        heading={heading}
        shortHeading={shortHeading}
        descriptionText={descriptionText}
        handleFiltering={handleFiltering}
        enableMenuFilter={enableMenuFilter}
      />
    </StyledNavigation>
  );
};

export { Menu, IMenuProps, IMenuPrimaryItem, IMenuSecondaryItem };
