import React from "react";
import { useResponsive } from "@faharmony/helpers";
import { IFABarProps } from "../types";
import VerticalBar from "./Vertical";
import HorizontalBar from "./Horizontal";

const FABar = (props: IFABarProps) => {
  const { isMobile } = useResponsive();
  return isMobile ? <HorizontalBar {...props} /> : <VerticalBar {...props} />;
};

export default FABar;
