import React from "react";
import { styled, Theme } from "@faharmony/theme";
import { Text } from "@faharmony/components";

interface IContentViewProps {
  /**
   * Title of the Area
   */
  headerTitle: string;
  /**
   * Actions on the top right of Area
   */
  headerActions?: React.ReactNode;
  /**
   * True if header area needs to be hidden
   */
  headerHidden?: boolean;
  /**
   * Content inside the Area
   */
  children: React.ReactNode;
  /**
   * Show or hide scroll bars
   * @default 'auto'
   */
  overflow?: "auto" | "scroll" | "hidden";
  /**
   * Show ContentView footer with custom content
   */
  footerContent?: React.ReactNode;

  /** @private Undocumented @default false */
  stretchHeight?: boolean;
}

const headerHeight = `calc(${Theme.spacing()} * 5)`;

// const maxDimensions = () => "calc(100vw - " + Theme.spacing("xl") + " )";

const StyledContainer = styled.div`
  /* margin: ${() => Theme.spacing()}; */
  box-shadow: ${() => Theme.shadow()};
  border: 1px solid ${() => Theme.colorOutline("border")};
  border-radius: ${() => Theme.radius()};
  height: 100%;
  max-height: 100%;
  max-width: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  background-color: ${() => Theme.colorBackground("base")};
`;

const StyledHeader = styled.div`
  border-bottom: 1px solid ${() => Theme.colorOutline("border")};
  padding: ${() => Theme.spacing("sm")} ${() => Theme.spacing()};
  height: ${headerHeight};
  min-height: ${headerHeight};
  width: 100%;
  display: grid;
  grid-template-columns: minmax(0, auto) max-content;
  background-color: transparent;
  gap: ${() => Theme.spacing("xl")};
  .ContentViewHeaderTitle {
    margin: ${() => `calc(${Theme.spacing("sm")} + ${Theme.spacing("xs")})`} 0;
  }
`;

const StyledBody = styled.div`
  position: relative;
  padding: 0;
  flex: auto;
  height: initial;
  width: 100%;
  /* background-color: ${Theme.colorBackground("elevated")}; */
`;

const StyledFooter = styled.div`
  border-top: 1px solid ${() => Theme.colorOutline("border")};
  padding: ${() => Theme.spacing("sm")} ${() => Theme.spacing()};
  width: "100%";
  height: ${headerHeight};
  min-height: ${headerHeight};
  background-color: transparent;
`;

/**
 * ContextView
 * ---
 * Content View can be placed inside other Views to encapsulate or differentiate between various content.
 *
 * It can be also used as base for Modals. When using inside modal, pass stretchHeight={true} param if modal needs to use maximum height.
 *
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @version 1.5.0
 * @example
 * <ContentView headerTitle="Heading"> {content} </ContentView>
 */
const ContentView = ({
  headerTitle,
  headerHidden = false,
  children,
  headerActions,
  footerContent,
  overflow = "auto",
  stretchHeight = false,
}: IContentViewProps) => {
  return (
    <StyledContainer
      className={`ContentView ${stretchHeight ? "stretchHeight" : ""}`}
    >
      {headerHidden ? null : (
        <StyledHeader className="ContentViewHeader">
          <div className="ContentViewHeaderTitle">
            <Text value={headerTitle} variant="h5" truncate />
          </div>
          <div className="ContentViewHeaderActions">{headerActions}</div>
        </StyledHeader>
      )}
      <StyledBody className="ContentViewBody" style={{ overflow }}>
        {children}
      </StyledBody>
      {footerContent && (
        <StyledFooter className="ContentViewFooter">
          {footerContent}
        </StyledFooter>
      )}
    </StyledContainer>
  );
};

export { ContentView, IContentViewProps };
export default ContentView;
