import React from "react";
import { Theme } from "@faharmony/theme";
import { Text, Grid, Icon } from "@faharmony/components";
import { IModuleProps } from "@faharmony/module";
import { IAppConfig } from "../types";
import { useCheckUserForRoles, combineModuleRoles } from "./helpers";

export default function LandingPage({
  appConfig,
  Modules,
}: {
  appConfig: IAppConfig;
  Modules: IModuleProps[];
  showModuleBadges?: boolean;
}) {
  const isAnythingAllowed = useCheckUserForRoles(combineModuleRoles(Modules));
  const description = !isAnythingAllowed ? (
    <Text
      value={`You do not have enough permissions to use this application.`}
      color="disabled"
    />
  ) : (
    undefined
  );
  return (
    <Grid
      style={{
        position: "relative",
        background: isAnythingAllowed
          ? Theme.colorBackground("base")
          : Theme.colorBackground("disabled"),
      }}
    >
      <Grid
        width="auto"
        height="auto"
        spacing="xl"
        gridTemplate="max-content max-content / auto"
        style={{
          position: "absolute",
          left: "50%",
          top: "50%",
          transform: "translateX(-50%) translateY(-50%)",
          color: Theme.colorText("disabled"),
          maxWidth: "800px",
          justifyItems: "center",
        }}
      >
        <Grid
          width="auto"
          height="auto"
          gridTemplate="auto / max-content auto"
          spacing="xl"
          style={{ maxWidth: "400px", textAlign: "left" }}
        >
          <Icon icon={appConfig.icon} size="5x" />
          <div style={{ paddingTop: Theme.spacing("lg") }}>
            <Text value="Welcome to" color="disabled" variant="h4" />
            <br />
            <Text value={appConfig.name} color="disabled" variant="h2" />
            {description ? (
              <span>
                <br />
                {description}
              </span>
            ) : null}
          </div>
        </Grid>
      </Grid>
    </Grid>
  );
}
