import React from "react";
import {
  localStorageKeys,
  getLocalStorageValue,
  setLocalStorageValue,
} from "@faharmony/helpers";
import { useInterval, usePropState } from "@faharmony/helpers";
import { getTextDimensions } from "@faharmony/theme";
// keyPressCombos,
// useKeyPress,
import { IMenuPrimaryItem, IMenuSecondaryItem, StyleProps } from "./types";

export const useMenuCollapse = (isMenuCollapsedDefault?: boolean) => {
  const [isMenuCollapsed, setMenuCollapsed] = React.useState(
    getLocalStorageValue(localStorageKeys.menuCollapsed) === "true"
  );
  // const isKeyPressed = useKeyPress(keyPressCombos.collapseNavigationMenu);
  // React.useEffect(() => {
  //   if (isKeyPressed) setMenuCollapsed((prev) => !prev);
  // }, [isKeyPressed]);

  React.useEffect(() => {
    setMenuCollapsed(
      isMenuCollapsedDefault ||
        getLocalStorageValue(localStorageKeys.menuCollapsed) === "true"
    );
  }, [isMenuCollapsedDefault]);
  React.useEffect(() => {
    setLocalStorageValue(
      localStorageKeys.menuCollapsed,
      isMenuCollapsed.toString()
    );
  }, [isMenuCollapsed]);
  const toggleMenuCollapse = React.useCallback(
    () => setMenuCollapsed((prev) => !prev),
    []
  );
  return { isMenuCollapsed, toggleMenuCollapse };
};

export const useVisibleText = (
  targetRef: React.RefObject<any>,
  isMenuCollapsed: boolean,
  text: string,
  shortText?: string
) => {
  const [state, setState] = React.useState({
    isHidden: false,
    visibleText: text,
  });
  const [elementHeight, setDimensions] = React.useState(0);

  React.useLayoutEffect(() => {
    if (targetRef.current) setDimensions(targetRef.current.offsetHeight);
  }, [isMenuCollapsed]);

  React.useEffect(() => {
    const availableSpace = elementHeight - 60;
    const { width: textLength } = getTextDimensions(text, "h1");
    const { width: shortTextLength } = shortText
      ? getTextDimensions(shortText, "h1")
      : { width: undefined };
    if (isMenuCollapsed) {
      if (textLength > availableSpace) {
        if (shortText && shortTextLength) {
          if (shortTextLength > availableSpace)
            setState({ visibleText: shortText, isHidden: true });
          else setState({ visibleText: shortText, isHidden: false });
        } else setState({ visibleText: text, isHidden: true });
      } else setState({ isHidden: false, visibleText: text });
    } else setState({ isHidden: false, visibleText: text });
  }, [elementHeight]);
  return state;
};

export const useCleanItems = (items: IMenuPrimaryItem[]) => {
  const [cleanItems, setItems] = React.useState(items);
  React.useEffect(() => {
    setItems(() => {
      let newItems = items
        .filter((item) => (item.id && item.id !== "" ? true : false))
        .filter(
          (value, index, self) =>
            self.map((x) => x.id).indexOf(value.id) == index
        );
      newItems = newItems.map((item) => ({
        ...item,
        subItems: item.subItems
          ? item.subItems
              .filter((subItem) =>
                subItem.id && subItem.id !== "" ? true : false
              )
              .filter(
                (value, index, self) =>
                  self.map((x) => x.id).indexOf(value.id) == index
              )
          : undefined,
      }));
      return newItems;
    });
  }, [items]);
  return cleanItems;
};

export const useFilterNavigationItems = (items: IMenuPrimaryItem[]) => {
  const [filteredItems, setItems] = usePropState(items);
  const handleFiltering = React.useCallback(
    (value: string) => {
      const newItems = items.map((item) => ({
        ...item,
        subItems: item.subItems?.filter((subItem) =>
          subItem.label.toLowerCase().includes(value.toLowerCase())
        ),
      }));
      setItems(newItems);
    },
    [items]
  );
  return { filteredItems, handleFiltering };
};

export const useCheckLeadingIcon = (items: IMenuPrimaryItem[]) => {
  const [showIcon, setShowIcon] = React.useState(false);
  React.useEffect(() => {
    if (items.filter((item) => item.icon).length) setShowIcon(true);
    else setShowIcon(false);
  }, [items]);
  return showIcon;
};

const isItemSelected = (item: IMenuPrimaryItem | IMenuSecondaryItem) =>
  item.isSelectedCallback ? item.isSelectedCallback() : false;

const itemSelectedId = (item: IMenuPrimaryItem) => {
  let selectedId = "";
  if (isItemSelected(item)) selectedId = item.id;
  if (item.subItems)
    item.subItems.forEach((subItem) => {
      if (isItemSelected(subItem)) selectedId = subItem.id;
    });
  return selectedId;
};

const getDefaultSelectedId = (items: IMenuPrimaryItem[]) => {
  const selectedIds = items.map(itemSelectedId).filter((id) => id !== "");
  if (selectedIds.length) return selectedIds[0];
  return "";
};

export const useSelected = (items: IMenuPrimaryItem[]) => {
  const [selectedId, setSelectedId] = React.useState<string>(
    getDefaultSelectedId(items)
  );
  const [windowLocation, setLocation] = React.useState<string>(
    window.location.hash
  );

  useInterval(() => setLocation(window.location.hash), 500);

  React.useEffect(() => {
    setSelectedId(getDefaultSelectedId(items));
  }, [items, windowLocation]);

  const handleItemSelect = React.useCallback(
    (item: IMenuPrimaryItem | IMenuSecondaryItem) => setSelectedId(item.id),
    []
  );
  return { selectedId, handleItemSelect };
};

// Body

export const useSubItemSelected = (
  item: IMenuPrimaryItem,
  selectedId: string
) => {
  const [subItemSelected, setSubItemSelected] = React.useState(false);
  React.useEffect(() => {
    if (item.subItems) {
      const selectedSubItems = item.subItems.filter(
        (subItem) => subItem.id === selectedId
      );
      setSubItemSelected(selectedSubItems.length ? true : false);
    } else setSubItemSelected(false);
  }, [item.subItems, selectedId]);
  return subItemSelected;
};

export const useExpandItem = (
  item: IMenuPrimaryItem,
  subItemSelected: boolean,
  selectedId: string
) => {
  const [canExpand, setCanExpand] = React.useState(false);
  const [isExpanded, setIsExpanded] = React.useState(
    item.id === selectedId && !!item.subItems
  );
  React.useEffect(() => {
    setIsExpanded(
      (subItemSelected || item.id === selectedId) && !!item.subItems
    );
  }, [subItemSelected, selectedId]);
  React.useEffect(() => {
    if (item.subItems && item.subItems.length) setCanExpand(true);
    else setCanExpand(false);
  }, [item.subItems]);

  const toggleExpand = React.useCallback(() => {
    setIsExpanded((prev) => !prev);
  }, []);
  return { canExpand, isExpanded, toggleExpand };
};

// COMMON

export const calcGridTemplateRows = (props: StyleProps) => {
  const { isHeaderVisible, isMenuCollapsed, actionsPresent } = props;

  let rows = "";

  if (isMenuCollapsed) {
    if (actionsPresent) rows += "max-content ";
    return (rows +=
      "max-content minmax(max-content, auto) minmax(auto,max-content)");
  } else {
    if (isHeaderVisible) rows += "max-content ";
    if (actionsPresent) rows += "max-content ";
    return (rows += "max-content 1fr max-content");
  }

  // if (!props.isHeaderVisible)
  //   return props.isMenuCollapsed ? "max-content 1fr" : "auto max-content";
  // else
  //   return props.isMenuCollapsed
  //     ? props.actionsPresent
  //       ? "max-content max-content auto"
  //       : "max-content auto"
  //     : "max-content auto max-content";
};
