import React from "react";
import { useResponsive } from "@faharmony/helpers";
import { getValidTagName } from "@faharmony/helpers";

interface IResponsiveProps {
  /**
   * If component should render in Mobile view
   */
  mobile?: boolean;
  /**
   * If component should render in Tablet view
   */
  tablet?: boolean;
  /**
   * If component should render in Desktop view
   */
  desktop?: boolean;
  /**
   * Component to render
   */
  children: React.ReactNode;
  /**
   * HTML tag to be used
   * @default div
   */
  tagName?: keyof HTMLElementTagNameMap;
  /**
   * Any class name to be attached
   */
  className?: string;
  /**
   * CSS Styles object
   */
  style?: React.CSSProperties;
}

/**
 * Responsive
 * ---
 * Wrap your component in this wrapper so it only renders in desired device size.
 * It uses **useResponsive** hook. Use the hook directly for more custom usage.
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @version 1.5.0
 * @example
 * <Responsive mobile>Visible in Mobile only</Responsive>
 * <Responsive tablet>Visible in Tablet only</Responsive>
 * <Responsive desktop>Visible in Desktop only</Responsive>
 */
const Responsive = ({
  mobile = false,
  tablet = false,
  desktop = false,
  children,
  tagName,
  style,
  className,
}: IResponsiveProps) => {
  const { isMobile, isTablet, isDesktop } = useResponsive();

  const Tag = getValidTagName(tagName);
  const element = React.createElement(Tag, { style, className }, children);

  if (mobile && !tablet && !desktop) {
    // Only mobile
    if (isMobile) return element;
  } else if (!mobile && tablet && !desktop) {
    // Only tablet
    if (isTablet) return element;
  } else if (!mobile && !tablet && desktop) {
    // Only desktop
    if (isDesktop) return element;
  } else if (mobile && tablet && !desktop) {
    // mobile and tablet
    if (isMobile || isTablet) return element;
  } else if (!mobile && tablet && desktop) {
    // tablet and desktop
    if (isDesktop || isTablet) return element;
  } else if (mobile && desktop) {
    // all
    return element;
  }
  // No match
  return null;
};

export { Responsive, IResponsiveProps };
