import React from "react";
import { Box } from "@faharmony/components";
import { useCheckLeadingIcon, useSelected } from "../helpers";
import {
  NavigationMenuProps,
  IMenuPrimaryItem,
  IMenuSecondaryItem,
} from "../types";
import { StyledNavigationMenu } from "./Style";
import PrimaryItem from "./PrimaryItem";

/**
 * NavigationMenu
 * ---
 * Builds a menu with collapsible items.
 * Used primarily in MenuView for showing App menu.
 *
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 *
 * ---
 *
 * @example
 * <NavigationMenu navItems={[
 *  {
 *    id: 'item1',
 *    label: 'Item 1',
 *    icon: faSquare,
 *    handleClick: (e) => console.log(e),
 *    isSelectedCallback: () => true,
 *  },
 * ]} />
 */
const NavigationMenu: React.FC<NavigationMenuProps> = (
  props: NavigationMenuProps
) => {
  const { navItems: items } = props;
  const showLeadingIcon = useCheckLeadingIcon(items);
  const { selectedId, handleItemSelect } = useSelected(items);
  const ref = React.useRef<HTMLElement>(null);
  const isMenuCollapsed = props.isMenuCollapsed;
  const renderItem = (item: IMenuPrimaryItem) => (
    <PrimaryItem
      key={item.id}
      item={item}
      showLeadingIcon={showLeadingIcon}
      isSelected={selectedId === item.id}
      handleItemSelect={handleItemSelect}
      // Don't expand by default
      isExpanded={false}
      selectedId={selectedId}
      isMenuCollapsed={isMenuCollapsed}
    />
  );

  const displayItems = items.map(renderItem);

  return (
    <StyledNavigationMenu
      isMenuCollapsed={isMenuCollapsed}
      isHeaderShown={props.isHeaderShown}
      ref={ref}
    >
      {displayItems.length ? (
        isMenuCollapsed ? (
          <Box direction="column">{displayItems}</Box>
        ) : (
          <ul>{displayItems}</ul>
        )
      ) : (
        !isMenuCollapsed && <div className="noContent">No content</div>
      )}
    </StyledNavigationMenu>
  );
};

export {
  NavigationMenu,
  IMenuPrimaryItem as INavPrimaryItem,
  IMenuSecondaryItem as INavSecondaryItem,
};
export default NavigationMenu;
