import React from "react";
import { styled } from "@faharmony/theme";
import { Theme } from "@faharmony/theme";
import { Button } from "../../Button";
import { Box } from "../../Box";
import { Text } from "../../Text";
import { IPopoverInstance, IPopoverConfirmProps } from "../types";

const Styled = styled.div`
  margin: 0;
  padding: ${() => Theme.spacing()};
  min-width: 200px;
  max-width: 320px;
`;

export default (props: IPopoverConfirmProps) => (
  instance: IPopoverInstance
) => {
  const {
    message = "Are you sure?",
    confirmButtonIcon,
    cancelButtonText = "No",
    confirmButtonText = "Yes",
    confirmButtonVariant = "primary",
    onCancel = () => {},
    onConfirm,
  } = props;

  const handleConfirm = () => {
    onConfirm();
    instance.closePopover();
  };
  const handleCancel = () => {
    onCancel();
    instance.closePopover();
  };

  return (
    <Styled>
      <Text value={message} />
      <Box justifyContent="flex-end" style={{ marginTop: Theme.spacing() }}>
        <Button
          value={cancelButtonText}
          onClick={handleCancel}
          outline
          spacing="sm"
          variant="secondary"
        />
        <Button
          value={confirmButtonText}
          icon={confirmButtonIcon}
          variant={confirmButtonVariant}
          onClick={handleConfirm}
          spacing="sm"
        />
      </Box>
    </Styled>
  );
};
