import Keycloak, { KeycloakInstance, KeycloakConfig } from "keycloak-js";
import { adminRealmRole } from "./common";

let authToken: string | undefined = undefined;
/**
 * Get current Keycloak auth token (encoded) string (if available)
 */
export const getAuthToken = () => authToken;

/**
 * Authenticate the app with Keycloak.
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @param config - as an object or public url to json file.
 * @param onAuthSuccess - Callback called when authentication is successful. Receives initialized keycloakInstance as an argument.
 * @param onAuthFailure - (Optional) Callback called when authentication fails. Receives no arguments.
 */
export const authenticateWithKeycloak = (props: {
  /**
   * Keycloak config as an object or public url to json file.
   */
  keycloakConfig: KeycloakConfig | string;
  /**
   * Callback called when authentication is successful. Receives initialized keycloakInstance as an argument.
   */
  onAuthSuccess: (keycloakInstance: KeycloakInstance) => void;
  /**
   * (Optional) Callback called when authentication fails. Receives no arguments.
   */
  onAuthFailure?: () => void;
}) => {
  const {
    keycloakConfig = { url: "/auth", realm: "fa", clientId: "fa-admin-app" },
    onAuthSuccess,
    onAuthFailure,
  } = props;

  const handleAuthSuccess = (keycloakInstance: KeycloakInstance) => {
    console.info("Logged in successfully.");
    /**
     * Handle user authorization. If user has no roles in current
     * client/app, then a warning is shown in console.
     * @todo Redirect unauthorised users to Root landing page.
     * Overridden be FA ADMIN.
     */
    // Get a list of client-Ids in for which user has a role.
    const clientIds = Object.entries(keycloakInstance.resourceAccess || {}).map(
      ([resource]) => resource
    );
    const currentClientId = keycloakInstance.clientId || "fa";
    if (
      !clientIds.includes(currentClientId) &&
      !keycloakInstance.hasRealmRole(adminRealmRole)
    ) {
      console.warn(
        "User doesn't have enough permissions to access this application."
      );
    }

    /**
     * If user has at least one role in for current app, then
     * execute onAuthSuccess, along with refreshing the token.
     */
    onAuthSuccess(keycloakInstance);
    authToken = keycloakInstance.token;
    setInterval(() => {
      // Token refresh
      keycloakInstance
        .updateToken(10)
        .then(function(refreshed: boolean) {
          if (refreshed) {
            authToken = keycloakInstance.token;
            console.info("Authentication token was successfully refreshed");
          }
        })
        .catch(function() {
          console.error(
            "Failed to refresh the token, or the session has expired"
          );
          keycloakInstance.clearToken();
          keycloakInstance.logout();
        });
    }, 5000);
  };

  // Init
  const instance = Keycloak(keycloakConfig);
  instance
    .init({ onLoad: "login-required", checkLoginIframe: false })
    .then((authenticated) => {
      if (authenticated) handleAuthSuccess(instance);
      else throw new Error("Authentication failed");
    })
    .catch((reason: any) => {
      if (instance.hasRealmRole(adminRealmRole)) {
        console.info("Logging in as FA ADMIN.");
        handleAuthSuccess(instance);
      } else {
        instance.clearToken();
        instance.logout();
        onAuthFailure ? onAuthFailure() : console.error(reason);
      }
    });
};

export default authenticateWithKeycloak;
