import React from "react";
import { Theme } from "@faharmony/theme";
import { Button, Divider, Box, Toolbar } from "@faharmony/components";
import { INavActionsProps } from "./types";

export const Actions = (props: INavActionsProps) => {
  const { actions, isMenuCollapsed } = props;

  const actionButtons = (list: INavActionsProps["actions"]) => (
    <Box
      justifyContent="space-between"
      wrap="nowrap"
      direction={"column"}
      className="navHeader-actions"
    >
      {list?.map((action) => (
        <Button {...action} key={action.id} />
      ))}
    </Box>
  );

  return actions && !!actions.length ? (
    isMenuCollapsed ? (
      <div>
        {actionButtons(actions)}
        <Divider spacing="sm" />
      </div>
    ) : (
      <div style={{ padding: Theme.spacing(), paddingBottom: 0 }}>
        <Toolbar actions={actions} />
      </div>
    )
  ) : null;
};

export default Actions;
