import React from "react";
import Tippy from "@tippyjs/react/headless";
import { preventDefaultOnClick } from "@faharmony/helpers";
import {
  IPopoverProps,
  IPopoverMenuProps,
  IPopoverInstance,
  IPopoverConfirmProps,
  IPopoverMenuItemProps,
} from "./types";
import usePopoverOptions from "./usePopoverOptions";
import MenuContent from "./content/Menu";
import ConfirmContent from "./content/Confirm";

/**
 * Popover
 * ---
 * Extend the functionality by adding a floating popup near the element.
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @version 1.5.0
 * @example
 * <Popover content={<div>{contentToShow}</div>}> {triggerElement} </Popover>
 */
const Popover = (props: IPopoverProps) => {
  const { options } = usePopoverOptions(props);
  const { trigger = "click" } = props;
  const child = React.Children.only(props.children);
  if (child) {
    const tippyChildren = React.createElement(child.type, {
      ...child.props,
      onClick:
        trigger === "click" ? preventDefaultOnClick : child.props.onContextMenu,
      onContextMenu:
        trigger === "contextmenu"
          ? preventDefaultOnClick
          : child.props.onContextMenu,
    });
    return <Tippy {...options}>{tippyChildren}</Tippy>;
  }
  console.warn(
    "Popup could not be attached to the children since it didn't pass: React.Children.only(props.children)"
  );
  return props.children;
};

/**
 * Popover.Menu
 * ---
 * Render menu in popover
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @version 1.5.0
 * @example
 * <Popover.Menu menuItems={[{
 *  id: 'item1', label: 'Item 1'
 * }]}> {triggerElement} </Popover.Menu>
 */
Popover.Menu = ({ children, menuItems, ...props }: IPopoverMenuProps) => {
  return (
    <Popover
      content={MenuContent(menuItems)}
      {...props}
      tippyProps={{ placement: "bottom" }}
    >
      {children}
    </Popover>
  );
};

/**
 * Popover.Confirm
 * ---
 * Get a popup to confirm an action
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @version 1.5.0
 * @example
 * <Popover.Confirm> {triggerElement} </Popover.Confirm>
 */
Popover.Confirm = (props: IPopoverConfirmProps) => {
  return (
    <Popover content={ConfirmContent(props)} {...props}>
      {props.children}
    </Popover>
  );
};

///////////////////////////////////
export {
  Popover,
  IPopoverProps,
  IPopoverInstance,
  IPopoverMenuItemProps,
  IPopoverMenuProps,
  IPopoverConfirmProps,
};
