import {
  getAuthToken,
  KeycloakTokenParsed,
  KeycloakResourceAccess,
} from "@faharmony/service";
import { IGlobalBarProps, IValidKeycloak } from "./types";

const AdminRole = "FA_ADMIN";
const realm = "fa";

/** Utility function to parse the authToken string
 * @param {string} authToken
 * @returns {ParsedToken}
 * "aaa.bbb.ccc"
 */
const getParsedToken = (authToken: string): KeycloakTokenParsed =>
  JSON.parse(btoa(authToken.split(".")[1]));

const getClientsFromResourceAccess = (
  resourceAccess: KeycloakResourceAccess = {}
) => Object.entries(resourceAccess).map(([resource]) => resource);

export const useValidKeycloak = ({
  authToken,
  keycloakInstance,
  _harmony,
  devMode,
}: IGlobalBarProps): IValidKeycloak => {
  // DevMode
  if (devMode || _harmony)
    return {
      accountUrl: "",
      logout: () => {},
      isAdmin: true,
      clients: [],
    };

  // Keycloak instance
  if (keycloakInstance?.token)
    return {
      accountUrl: keycloakInstance.createAccountUrl(),
      logout: keycloakInstance.logout,
      isAdmin: keycloakInstance.hasRealmRole(AdminRole),
      clients: getClientsFromResourceAccess(keycloakInstance.resourceAccess),
    };

  // Auth token
  const token = getAuthToken() || authToken;
  if (token) {
    const parsedToken = getParsedToken(token);
    const clients = getClientsFromResourceAccess(parsedToken.resource_access);
    const isAdmin =
      parsedToken.realm_access?.roles.includes(AdminRole) || false;

    const accountUrl = `/auth/realms/${realm}/account/`;

    const logoutUrl = `/auth/realms/${realm}/protocol/openid-connect/logout?redirect_uri=${encodeURI(
      window.location.href
    )}`;
    const logout = () => window.open(logoutUrl, "_self");

    return {
      isAdmin,
      accountUrl,
      clients,
      logout,
    };
  }

  // Nothing
  return {
    accountUrl: "",
    logout: () => {},
    isAdmin: false,
    clients: [],
  };
};
