import React from "react";
import { styled } from "@faharmony/theme";
import { Theme } from "@faharmony/theme";
import { Button, IIconButtonObject } from "../Button";

const Wrapper = styled.div`
  border-radius: ${() => Theme.radius()};
  width: 100%;
  background-color: ${() => Theme.colorBackground("disabled")};
  /* box-shadow: ${Theme.props.shadow.inner}; */
  padding: ${() => Theme.spacing()};
  min-height: 44px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
`;

interface IToolbarProps {
  actions?: IIconButtonObject[];
  children?: React.ReactNode;
}

const Toolbar = ({ actions, children }: IToolbarProps) => {
  if (actions || children)
    return (
      <Wrapper>
        {actions?.map((action) => (
          <Button
            key={action.id}
            variant={"secondary"}
            {...action}
            spacing="sm"
          />
        )) || children}
      </Wrapper>
    );
  else {
    console.warn(
      "Toolbar could not be rendered since no actions or children were passed."
    );
    return null;
  }
};

export { Toolbar, IToolbarProps };
