import React, { useContext } from "react";
import { useTranslation } from "react-i18next";
import { IModuleWithReturnProps, IModuleProps } from "./types";

const ModuleContext = React.createContext<IModuleProps>({} as IModuleProps);

const ModuleProvider = (props: {
  Module: IModuleProps;
  children: React.ReactNode;
}) => {
  return (
    <ModuleContext.Provider value={props.Module}>
      {props.children}
    </ModuleContext.Provider>
  );
};

/**
 * Hook to get Module information inside the module
 * with locale T function.
 */
const useModule = (): IModuleWithReturnProps => {
  const Module = useContext(ModuleContext);
  const t = useTranslation(Module.id).t;
  return { ...Module, t };
};

export { ModuleProvider, useModule };
