import { useMemo } from "react";
import { useMediaQuery } from "../sensors/useMediaQuery";

type DeviceType = "desktop" | "tablet" | "mobile";

/**
 * Hook to check device-type (size).
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @example
 * const {deviceType, minWidth, maxWidth, isMobile, isTablet, isDesktop} = useResponsive();
 */
export const useResponsive = () => {
  const deviceType: DeviceType = useMediaQuery(
    ["(min-width: 1024px)", "(min-width: 600px)", "(min-width: 0px)"],
    ["desktop", "tablet", "mobile"],
    "desktop"
  );

  const minWidth: number = useMemo(
    () => (deviceType === "mobile" ? 0 : deviceType === "tablet" ? 600 : 1024),
    [deviceType]
  );
  const maxWidth: number = useMemo(
    () =>
      deviceType === "mobile" ? 599 : deviceType === "tablet" ? 1023 : Infinity,
    [deviceType]
  );
  const isMobile: boolean = useMemo(() => deviceType === "mobile", [
    deviceType,
  ]);
  const isTablet: boolean = useMemo(() => deviceType === "tablet", [
    deviceType,
  ]);
  const isDesktop: boolean = useMemo(() => deviceType === "desktop", [
    deviceType,
  ]);

  return { deviceType, minWidth, maxWidth, isMobile, isTablet, isDesktop };
};

export default useResponsive;
