import axios, { AxiosRequestConfig } from "axios";
import { getAuthToken } from "../keycloak";
import { useAxios } from "./useAxios";

export const faKeycloakApiInstance = axios.create({
  baseURL: `${process.env.REACT_APP_KEYCLOAK_URL || "/auth"}/admin/realms/fa/`,
});
faKeycloakApiInstance.interceptors.request.use(
  (config) => {
    config.headers.Authorization = `Bearer ${getAuthToken()}`;
    return config;
  },
  (error) => Promise.reject(error)
);

/**
 * useKeycloakApi
 * ---
 * Use FA's Keycloak api available at `/auth` endpoint.
 * @param  options
 * @param  lazy [false] - true: to prevent execution onMount.
 * @example
 * const [state, execute, cancel] = useKeycloakApi<DataType>({url: "users", method: "GET"});
 * const { isLoading, isCancelled, error, success, data} = state;
 */
export const useKeycloakApi = <T = any>(
  options: AxiosRequestConfig,
  lazy: boolean = false
) => useAxios<T>(options, faKeycloakApiInstance, lazy);
