/* eslint-disable react/display-name */
//https://github.com/tannerlinsley/react-table/

import { styled, Theme } from "@faharmony/theme";

interface StyleProps {
  handleRowClick: any;
  handleRowDblClick: any;
}

export const Styles = styled.div<StyleProps>`
  /* This is required to make the table full-width */
  display: block;
  max-width: 100%;
  width: 100%;
  height: 100%;
  min-height: 120px;
  background-color: ${() => Theme.colorBackground("base")};

  table {
    table-layout: fixed;
    width: auto;
    min-width: 100%;
    border-spacing: 0;
    position: relative;

    thead {
      tr {
        min-height: 40px;        

        th {
          position: sticky;
          top: 0;
          background-color: ${() => Theme.colorBackground("disabled")};
          border-bottom: 1px solid ${() => Theme.colorOutline("border")};
          border-right: 1px solid ${() => Theme.colorOutline("border")};
          min-height: 40px;
          z-index: 200;

          margin: 0;
          text-align: left;
          padding: ${() => Theme.spacing()};

          :last-child {
            border-right: 0;
          }

          &.fixedColumnStart,
          &.fixedColumnEnd {
            position: sticky;
            z-index: 250;
          }
          &.fixedColumnStart {
            left: 0;
          }
          &.fixedColumnEnd {
            right: 0;
          }

          .table-header-sort {
            /* position: absolute;
            right: ${() => Theme.spacing("sm")};
            top: ${() => Theme.spacing()}; */
            color: ${() => Theme.colorText("disabled")};
            opacity: 0.5;
            cursor: pointer;

            :hover {
              opacity: 1;
            }
          }
        }
      }

      .filterRow {
        th {
          position: sticky;
          top: 40px;
          padding: ${() => Theme.spacing("sm")};
          background-color: ${() => Theme.colorBackground("disabled")};
          z-index: 250;

          input,
          button,
          select {
            font-family: inherit;
            color: ${() => Theme.colorText("primary")};
            background-color: ${() => Theme.colorBackground("base")};
            border-radius: ${() => Theme.radius("xs")};
            height: 28px;
            border: 1px solid ${() => Theme.colorOutline("border")};
            padding: 0 4px;
          }

          button {
            width: 28px;
          }
        }
      }
    }

    tbody tr {
      position: relative;
      z-index: 100;
      cursor: ${(p) =>
        p.handleRowClick || p.handleRowDblClick ? "pointer" : "initial"};

      td {
        margin: 0;
        text-align: left;
        padding: ${() => Theme.spacing()};
        background-color: ${() => Theme.colorBackground("base")};

        :last-child {
          border-right: 0;
        }

        &.fixedColumnStart,
        &.fixedColumnEnd {
          position: sticky;
          z-index: 150;
        }
        &.fixedColumnStart {
          left: 0;
        }
        &.fixedColumnEnd {
          right: 0;
        }
      }

      &:nth-of-type(odd),
      &:nth-of-type(odd) td {
        background-color: ${() => Theme.colorBackground("rowAlt")};
      }

      &:last-child {
        td {
          border-bottom: 0;
        }
      }

      &.selected,
      &.selected td {
        background-color: ${() => Theme.colorBackground("selected")} !important;
      }

      &:hover,
      &:hover td {
        background-color: ${() => Theme.colorBackground("hover")} !important;
      }

      &.subComponent td {
        padding: 0;
        margin: 0;
        background-color: ${() => Theme.colorBackground("elevated")} !important;
      }
    }

    tfoot {
      position: sticky;
      z-index: 200;
      bottom: 0;
      background-color: ${() => Theme.colorBackground("base")};
      z-index: 200;

      tr:first-of-type {
        td {
          border-top: 1px solid ${() => Theme.colorOutline("border")};
          border-right: 1px solid ${() => Theme.colorOutline("border")};
          :last-child {
            border-right: 0;
          }
        }
      }
    }
  }
`;

export default Styles;
