import {
  IThemeProps,
  TintType,
  ITextColors,
  IBackgroundColors,
  IOutlineColors,
  ITintColors,
  SMLType,
} from "./types";
import defaultTheme from "./defaultTheme";

export const HarmonyColor = "#32127A";

// List of zIndex
export const zIndices = {
  hide: -1,
  menu: 0,
  base: 50,
  docked: 100,
  sticky: 1100,
  banner: 1200,
  overlay: 1300,
  modal: 1400,
  popover: 1500,
  skipLink: 1600,
  toast: 1700,
  contextMenu: 1800,
  tooltip: 2000,
};

// List of Shadows
export const shadows = {
  default: "0 0 var(--spacing-md) 0 var(--colors-outline-shadow)",
  large: "0 0 var(--spacing-xl) 0 var(--colors-outline-shadow)",
  hover: "0 0 var(--spacing-md) 0 var(--colors-outline-shadowHover)",
  active: "0 0 var(--spacing-sm) 0 var(--colors-outline-shadowActive)",
  outline: "0 0 1px 1px var(--colors-outline-border)",
  outlineInner: "inset 0 0 1px 1px var(--colors-outline-border)",
  inner: "inset 0 0 var(--spacing-sm) 0 var(--colors-outline-shadow)",
  float: "0 0 var(--spacing-md) var(--spacing-xs) var(--colors-outline-shadow)",
};

//List of transition
export const transitions = {
  spring: {
    type: "spring",
    damping: 50,
    stiffness: 300,
  },
};

export const getFontName = (
  text: IThemeProps["text"] | IThemeProps["code"]
): string => {
  const config = { ...text.fontConfig };
  if (config.google)
    return config.google.families
      .map((family) => family.split(":")[0])
      .join(", ");
  else if (config.typekit) return config.typekit.id || "";
  else if (config.fontdeck) return config.fontdeck.id || "";
  else if (config.monotype) return config.monotype.projectId || "";
  else if (config.custom && config.custom.families)
    return config.custom.families.join(", ") || "";
  return "Muli";
};

export const getColorTint = (type: TintType = "accent", opacity = 100) => {
  // Semantic colors
  if (type === "danger") type = "red";
  if (type === "warning") type = "orange";
  if (type === "success") type = "green";
  if (type === "info") type = "blue";

  let str = `var(--colors-tint-${type}`;
  opacity = Math.round(Math.abs(opacity) / 10) * 10;
  if (opacity > 100) opacity = 100;
  if (opacity < 0) opacity = 0;
  str += "-" + opacity.toString();
  return str + ")";
};

const SML = ["xs", "sm", "md", "lg", "xl", "none"];

export const getProps = (theme: IThemeProps = defaultTheme) => ({
  // Radius
  radius: SML.reduce((result, item) => {
    result[item] = `var(--radius-${item})`;
    return result;
  }, {} as Record<SMLType, string>),
  // Spacing
  spacing: SML.reduce((result, item) => {
    result[item] = `var(--spacing-${item})`;
    return result;
  }, {} as Record<SMLType, string>),

  zIndex: zIndices,
  shadow: shadows,
  transition: transitions,
  fontName: getFontName(theme.text),

  colorText: Object.entries(theme.lightColors.text)
    .map((type) => type[0])
    .reduce((result, item) => {
      result[item] = `var(--colors-text-${item})`;
      return result;
    }, {} as Record<keyof ITextColors, string>),

  colorBackground: Object.entries(theme.lightColors.background)
    .map((type) => type[0])
    .reduce((result, item) => {
      result[item] = `var(--colors-background-${item})`;
      return result;
    }, {} as Record<keyof IBackgroundColors, string>),

  colorOutline: Object.entries(theme.lightColors.outline)
    .map((type) => type[0])
    .reduce((result, item) => {
      result[item] = `var(--colors-outline-${item})`;
      return result;
    }, {} as Record<keyof IOutlineColors, string>),

  colorTint: Object.entries(theme.lightColors.tint)
    .map((type) => type[0])
    .reduce((result, item) => {
      result[item] = `var(--colors-tint-${item}-100)`;
      return result;
    }, {} as Record<keyof ITintColors, string>),
});
