import { OnClickEventType } from "./typeDefinitions";

export const NOOP = () => null;

export const preventDefaultOnClick = (e: OnClickEventType) => {
  e?.preventDefault();
  e?.stopPropagation();
};

/**
 * Returns a valid HTML tag which can be used for building components.
 * @param tagName
 */
export const getValidTagName = (tagName?: keyof HTMLElementTagNameMap) => {
  let Tag = "div";
  if (
    tagName !== undefined &&
    tagName !== "applet" &&
    tagName !== "basefont" &&
    tagName !== "dir" &&
    tagName !== "font" &&
    tagName !== "frame" &&
    tagName !== "frameset" &&
    tagName !== "marquee" &&
    tagName !== "slot"
  ) {
    Tag = tagName;
  }
  return Tag;
};
