import React from "react";
import { KeycloakInstance } from "keycloak-js";
import {
  ApolloProvider,
  ApolloClient,
  ApolloLink,
  createHttpLink,
  InMemoryCache,
} from "@apollo/client";
import { setContext } from "@apollo/client/link/context";
import { RestLink } from "apollo-link-rest";
import { getAuthToken } from "../keycloak";

const GraphQLProvider = (props: {
  children: React.ReactNode;
  keycloakInstance: KeycloakInstance;
}) => {
  // GraphQL
  const httpLink = createHttpLink({
    uri: "/graphql",
  });

  // REST
  const restLink = new RestLink({
    uri: "/services/",
    endpoints: {
      services: "/services/",
      legacy: "/rs/secure/fa/api/",
      keycloak: `${process.env.REACT_APP_KEYCLOAK_URL ||
        "/auth"}/admin/realms/fa/`,
    },
  });

  // Auth
  const authLink = setContext((_, { headers }) => {
    // get the authentication token from local storage if it exists
    const token = props?.keycloakInstance?.token || getAuthToken();
    // return the headers to the context so httpLink can read them
    return {
      headers: {
        ...headers,
        authorization: token ? `Bearer ${token}` : "",
        Accept: "application/json",
      },
    };
  });

  const faGraphQlClient = new ApolloClient<any>({
    // link: authLink.concat(httpLink)
    link: ApolloLink.from([authLink, restLink, httpLink]),
    cache: new InMemoryCache(),
  });

  return (
    <ApolloProvider client={faGraphQlClient}>{props.children}</ApolloProvider>
  );
};

export { GraphQLProvider };
