import React from "react";
import { styled } from "@faharmony/theme";
import { Text, ITextProps } from "../Text";

export const Wrapper = styled.div`
  width: 100%;
`;

interface IInfoBoxProps {
  /** The field name */
  field: string;
  /** Content (string) to be shown */
  value?: string;
  /** Content (any) to be shown */
  children?: React.ReactChild;
  /** Color of value text */
  color?: ITextProps["color"];
  /** Label is in same line ? @default false */
  horizontal?: boolean;
}

/**
 * InfoBox
 * ---
 * Show information in simple manner
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @version 1.6.0
 * @example
 * <InfoBox field="Name" value="Siddhant Gupta" />
 * --
 * <InfoBox field="Name"><Tag value="Unavailable"/></InfoBox>
 */
const InfoBox = ({
  field,
  value,
  children,
  color,
  horizontal = false,
}: IInfoBoxProps) => {
  return (
    <Wrapper>
      <Text variant="h6" value={field} color="primary" />
      {horizontal ? ": " : <br />}
      {children ||
        (value && (
          <span style={{ wordBreak: "break-all" }}>
            <Text value={value} color={color} />
          </span>
        )) ||
        null}
    </Wrapper>
  );
};

export { InfoBox, IInfoBoxProps };
