import { useState, useEffect } from "react";

/**
 * This hook makes it super easy to utilize media queries in your component logic. 
 * Both queries and values array should be equal in length.
 * @author Gabe Ragland <> https://github.com/gragland
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @example
 *  const elementWidth = useMediaQuery(
    // Media queries
    ['(min-width: 1500px)', '(min-width: 1000px)', '(min-width: 600px)'],
    // Element width (relates to above media queries by array index)
    [600, 400, 300],
    // Default element width
    200
  );
 */
export function useMediaQuery(
  queries: string[],
  values: any[],
  defaultValue: any
) {
  if (queries.length !== values.length) return defaultValue;

  // Array containing a media query list for each query
  const mediaQueryLists = queries.map(q => window.matchMedia(q));

  // Function that gets value based on matching media query
  const getValue = () => {
    // Get index of first media query that matches
    const index = mediaQueryLists.findIndex(mql => mql.matches);
    // Return related value or defaultValue if none
    return typeof values[index] !== "undefined" ? values[index] : defaultValue;
  };

  // State and setter for matched value
  const [value, setValue] = useState(getValue);

  useEffect(
    () => {
      // Event listener callback
      // Note: By defining getValue outside of useEffect we ensure that it has ...
      // ... current values of hook args (as this hook callback is created once on mount).
      const handler = () => setValue(getValue);
      // Set a listener for each media query with above handler as callback.
      mediaQueryLists.forEach(mql => mql.addListener(handler));
      // Remove listeners on cleanup
      return () => mediaQueryLists.forEach(mql => mql.removeListener(handler));
    },
    [] // Empty array ensures effect is only run on mount and un-mount
  );

  return value;
}
