import React, { useState, useCallback, useRef, RefObject } from "react";
import { createPortal } from "react-dom";
import { styled } from "@faharmony/theme";
import { Theme } from "@faharmony/theme";
import { useOnClickOutside } from "@faharmony/helpers";

const StyledDiv = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: ${() => Theme.zIndex("overlay")};
  background-color: ${() => Theme.colorBackground("overlay")};

  .modalContainer {
    position: relative;
    z-index: ${() => Theme.zIndex("modal")};
    overflow: auto;
    min-width: 320px;
    max-width: calc(100vw - ${() => Theme.spacing("xl")});
    max-height: calc(100vh - ${() => Theme.spacing("xl")});
    background-color: ${() => Theme.colorBackground("elevated")};
    border-radius: ${() => Theme.radius()};
    box-shadow: ${() => Theme.shadow("outlineInner")},
      ${() => Theme.shadow("float")};
  }
`;

interface ModalProps {
  isOpen?: boolean;
  children: React.ReactNode;
  style?: React.CSSProperties;
}

// eslint-disable-next-line react/display-name
const Modal = React.forwardRef<HTMLDivElement, ModalProps>((props, ref) => {
  const { children, isOpen = false, style = {} } = props;
  if (isOpen === false) {
    return null;
  }
  return createPortal(
    <StyledDiv className={"modalWrapper"}>
      <div className={"modalContainer"} ref={ref} style={style}>
        {children}
      </div>
    </StyledDiv>,
    document.getElementsByTagName("body")[0]
  );
});

/**
 * useModal
 * ---
 * Hook to create FA style Modals
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @version 1.5.0
 * @param closeOnClickOutside Pass false if modal should not close when clicked outside. By default, it is true.
 * @return [ModalWrapper, openModalFn, closeModalFn, isModalOpen]
 * - ModalWrapper - Wrap the Modal content with this element
 * - openModalFn - Callback to open the modal
 * - closeModalFn - Callback to close the modal
 * - isModalOpen - Boolean value to tell if modal is currently open
 *
 * ---
 *
 * @example
 * const [ModalWrapper, openModalFn, closeModalFn, isModalOpen] = useModal();
 */
export const useModal = (closeOnClickOutside = true) => {
  const ref: RefObject<HTMLDivElement> = useRef<HTMLDivElement>(null);
  const [isOpen, setOpen] = useState(false);
  const openModalFn = useCallback(() => setOpen(true), [setOpen]);
  const closeModalFn = useCallback(() => setOpen(false), [setOpen]);
  useOnClickOutside(ref, () => closeOnClickOutside && setOpen(false));

  const ModalWrapper = (props: {
    children: React.ReactNode;
    style?: React.CSSProperties;
  }) => (
    <Modal isOpen={isOpen} ref={ref} style={props.style}>
      {props.children}
    </Modal>
  );

  return [ModalWrapper, openModalFn, closeModalFn, isOpen] as const;
};
