import React from "react";
import Div100vh from "react-div-100vh";
import { useResponsive } from "@faharmony/helpers";
import { Theme } from "@faharmony/theme";
import { Text, Divider, Grid, Toolbar } from "@faharmony/components";
import { Breadcrumbs, IBreadcrumb } from "@faharmony/router";
import { IPageViewProps, IPageViewTabProps } from "./types";
import { StyledBody, StyledFooter, StyledHeader, StyledView } from "./style";
import { TabSwitcher, useCurrentTab } from "./Tabs";

const getCrumbs = (
  caption: IPageViewProps["caption"],
  heading: IPageViewProps["heading"],
  showTabSwitcher: boolean = false
) => {
  const crumbs: IBreadcrumb[] = caption
    ? typeof caption === "string"
      ? [
          {
            id: "default",
            label: caption,
          },
        ]
      : caption
    : [];
  if (showTabSwitcher) crumbs.push({ id: "heading", label: heading });
  return crumbs;
};

const PageViewHeader = (
  props: IPageViewProps & { currentTabId: string | null }
) => {
  const { heading, actions, tabs, currentTabId, caption } = props;
  const showTabSwitcher = tabs && tabs.length > 1;
  const [crumbs, setCrumbs] = React.useState<IBreadcrumb[]>([]);
  React.useEffect(() => {
    setCrumbs(() => getCrumbs(caption, heading, showTabSwitcher));
  }, [caption, heading]);

  const headingArea =
    showTabSwitcher && tabs ? (
      <TabSwitcher tabs={tabs} currentTabId={currentTabId} />
    ) : (
      <Text value={heading} variant="h2" truncate />
    );

  return (
    <StyledHeader id="pageView-header">
      <Grid
        gridTemplate="auto / minmax(0, auto) max-content"
        overflow="inherit"
        style={{ padding: `0 ${Theme.spacing()}` }}
      >
        <div
          id="pageView-header-title"
          style={{
            marginTop: crumbs.length ? 0 : Theme.spacing(),
            overflow: "hidden",
          }}
        >
          {crumbs.length > 0 && (
            <Breadcrumbs crumbs={crumbs} variant="strong" />
          )}
          {headingArea}
        </div>
        {actions && <div id="pageView-header-actions">{actions}</div>}
      </Grid>
    </StyledHeader>
  );
};

const PageViewBody = ({
  currentTabId,
  tabs = [],
  overflow,
}: {
  currentTabId: string | null;
  tabs: IPageViewProps["tabs"];
  overflow?: IPageViewProps["overflow"];
}) => {
  const visibleContent: React.ReactNode = React.useMemo(
    () =>
      currentTabId ? (
        tabs.find((tab) => tab.id === currentTabId)?.content
      ) : tabs.length ? (
        tabs[0].content
      ) : (
        <div />
      ),
    [currentTabId, tabs]
  );
  return (
    <StyledBody id="pageView-body" overflow={overflow}>
      {visibleContent}
    </StyledBody>
  );
};

const PageViewFooter = (props: IPageViewProps) => (
  <StyledFooter>
    <Divider />
    <div id="pageView-footer-content">{props.footerContent}</div>
  </StyledFooter>
);

/**
 * PageView
 * ---
 * Page View is the primary view which is used as base for all pages in FA App.
 *
 * It should NOT be used as base for Modals. See SummaryView/ContactView for that.
 *
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @version 1.5.0
 * @example
 * <PageView headerTitle="Heading"> {content} </PageView>
 */
const PageView = (props: IPageViewProps) => {
  const { toolbarContent, footerContent, overflow } = props;
  const { isMobile } = useResponsive();
  const { tabs, currentTabId } = useCurrentTab(props);
  return (
    <Div100vh style={{ height: isMobile ? "calc(100rvh - 48px)" : "100rvh" }}>
      <StyledView
        className="pageView"
        hasFooter={!!footerContent}
        hasToolbar={!!toolbarContent}
      >
        <PageViewHeader {...props} currentTabId={currentTabId} tabs={tabs} />
        {toolbarContent && (
          <div
            className={"pageView-toolbar"}
            style={{ padding: Theme.spacing(), paddingBottom: 0 }}
          >
            <Toolbar>{toolbarContent}</Toolbar>
          </div>
        )}
        <div style={{ margin: `${Theme.spacing()} ${Theme.spacing()} 0` }}>
          <Divider spacing="none" />
        </div>
        <PageViewBody
          tabs={tabs}
          currentTabId={currentTabId}
          overflow={overflow}
        />
        {footerContent && <PageViewFooter {...props} />}
      </StyledView>
    </Div100vh>
  );
};

export { PageView, IPageViewProps, IPageViewTabProps };
