import React from "react";
import { Theme, styled, getTextDimensions } from "@faharmony/theme";
import { Text } from "@faharmony/components";
import { INavCommonProps } from "./types";

const headerHeight = 50;

const StyledHeader = styled.header<INavCommonProps>`
  display: ${(props) => (props.isMenuCollapsed ? "block" : `grid`)};
  width: inherit;
  background: transparent;
  height: ${(props) => (props.isMenuCollapsed ? "unset" : `${headerHeight}px`)};
  max-height: ${(props) =>
    props.isMenuCollapsed ? "unset" : `${headerHeight}px`};
  overflow: hidden;
  padding: ${(props) =>
      props.isMenuCollapsed ? Theme.spacing() : Theme.spacing("lg")}
    ${() => Theme.spacing()} 0;
  grid-template-rows: auto max-content;

  .heading {
    margin: 0 ${() => Theme.spacing()};
    width: calc(inherit - 2 * ${Theme.spacing()});
    overflow: hidden;
  }

  > .navHeader-actions {
    margin-bottom: ${() => Theme.spacing()};
  }
`;

/**
 * Navigation Header
 * ---
 * Builds the navigation Header containing heading
 * and actions.
 *
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 */
const Header = (props: INavCommonProps) => {
  const { heading, shortHeading, isMenuCollapsed } = props;
  // Check whether to use short or normal heading
  const showShortHeading =
    getTextDimensions(heading, "h1").width > 210 && !!shortHeading;

  return isMenuCollapsed ? null : (
    <StyledHeader {...props}>
      <div className="heading">
        <Text
          truncate
          variant={"h1"}
          value={showShortHeading ? shortHeading || "" : heading}
        />
      </div>
    </StyledHeader>
  );
};

export default Header;
