import React from "react";
import { Provider } from "react-redux";
import { Slice, combineReducers, configureStore } from "@reduxjs/toolkit";

const convertArrayToObject = (array: Slice[]) => {
  const initialValue: { [name: string]: Slice["reducer"] } = {};
  return array.reduce((obj, item) => {
    return {
      ...obj,
      [item["name"]]: item.reducer,
    };
  }, initialValue);
};

/** Wrapper to provide global state management to app */
const StateProvider: React.FC<{ slices: Slice<any, any, any>[] }> = ({
  children,
  slices,
}) => {
  if (slices.length > 0) {
    const rootReducer = combineReducers(convertArrayToObject(slices));
    const store = configureStore({
      reducer: rootReducer,
    });
    return <Provider store={store}>{children}</Provider>;
  } else {
    return <React.Fragment>{children}</React.Fragment>;
  }
};

export { StateProvider };
