import React from "react";
import { TableToggleCommonProps2 } from "react-table";
import { OnClickEventType } from "@faharmony/helpers";
import { faSquare, faMinusSquare, faCheckSquare } from "@faharmony/icons";
import { Icon } from "@faharmony/components";

type InputRef =
  | React.RefObject<HTMLInputElement>
  | React.MutableRefObject<HTMLInputElement | undefined>;
export function isRef(
  ref: ((instance: HTMLInputElement | null) => void) | InputRef
): ref is InputRef {
  return (ref as InputRef).current !== undefined;
}

const Checkbox = (props: TableToggleCommonProps2) => {
  const { indeterminate, checked, onChange, title } = props;

  const handleClick = (event: OnClickEventType) => {
    event?.stopPropagation();
    onChange && onChange(event);
  };
  const icon = (
    <Icon
      onClick={handleClick}
      icon={checked ? faCheckSquare : indeterminate ? faMinusSquare : faSquare}
    />
  );
  return (
    <div style={{ cursor: "pointer" }} className={`table-checkbox-${title}`}>
      {icon}
    </div>
  );
};

export default Checkbox;
