/* eslint-disable @typescript-eslint/ban-ts-ignore */
import { useMemo, SetStateAction, useState, useCallback } from "react";

type UseStateful<T = any> = {
  value: T;
  setValue: React.Dispatch<SetStateAction<T>>;
};

type UseArrayActions<T> = {
  setValue: UseStateful<T[]>["setValue"];
  add: (value: T | T[]) => void;
  push: (value: T | T[]) => void;
  pop: () => void;
  shift: () => void;
  unshift: (value: T | T[]) => void;
  clear: () => void;
  move: (from: number, to: number) => void;
  removeById: (
    id: T extends { id: string }
      ? string
      : T extends { id: number }
      ? number
      : unknown
  ) => void;
  modifyById: (
    id: T extends { id: string }
      ? string
      : T extends { id: number }
      ? number
      : unknown,
    newValue: Partial<T>
  ) => void;
  removeIndex: (index: number) => void;
};

// type UseArray<T> = UseStateful<T[]> & UseArrayActions<T>;

function useArrayOriginal<T extends { id: string }>(initial: T[]) {
  const [value, setValue] = useState(initial);
  const push = useCallback(a => {
    setValue(v => [...v, ...(Array.isArray(a) ? a : [a])]);
  }, []);
  const unshift = useCallback(
    a => setValue(v => [...(Array.isArray(a) ? a : [a]), ...v]),
    []
  );
  const pop = useCallback(() => setValue(v => v.slice(0, -1)), []);
  const shift = useCallback(() => setValue(v => v.slice(1)), []);
  const move = useCallback(
    (from: number, to: number) =>
      setValue(it => {
        const copy = it.slice();
        copy.splice(to < 0 ? copy.length + to : to, 0, copy.splice(from, 1)[0]);
        return copy;
      }),
    []
  );
  const clear = useCallback(() => setValue(() => []), []);
  // @ts-ignore not every array that you will pass down will have object with id field.
  const removeById = useCallback(
    id => setValue(arr => arr.filter(v => v && v.id !== id)),
    []
  );
  const removeIndex = useCallback(
    index =>
      setValue(v => {
        const copy = v.slice();
        copy.splice(index, 1);
        return copy;
      }),
    []
  );
  const modifyById = useCallback(
    // @ts-ignore not every array that you will pass down will have object with id field.
    (id, newValue) =>
      setValue(arr => arr.map(v => (v.id === id ? { ...v, ...newValue } : v))),
    []
  );
  const actions: UseArrayActions<T> = useMemo(
    () => ({
      setValue,
      add: push,
      unshift,
      push,
      move,
      clear,
      removeById,
      removeIndex,
      pop,
      shift,
      modifyById
    }),
    [push, unshift, move, clear, removeById, removeIndex, pop, shift]
  );
  return { value, actions };
}

/**
 * Hook to efficiently maintain arrays.
 * @author Andrey Los <> https://github.com/RIP21
 * @author Alberto <> https://github.com/nkint
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @param initial Array of values
 *
 * ---
 *
 * @example
 * const todos = useArray(['Item1', 'Item2']);  
  //todos.push(newValue);
  //todos.pop();
  //todos.unshift(value);
  //todos.shift();
  //todos.clear();
  //todos.move(fromIndex, toIndex);
  //todos.removeById(id); // requires Object[] with "id" param
  //todos.modifyById(id, newValue); // requires Object[] with "id" param
  //todos.removeIndex(index);
 */
export function useArray<T extends { id: string }>(initial: T[]) {
  const { value, actions } = useArrayOriginal(initial);
  return useMemo(
    () => ({
      value,
      ...actions
    }),
    [actions, value]
  );
}

export default useArray;
