import { i18n, Resource, InitOptions } from "i18next";
import { useModule } from "@faharmony/module";
import {
  languageCodeLocalStorageKey,
  getLocalStorageValue,
  setLocalStorageValue,
} from "@faharmony/helpers";
import languageCodes, { LanguageCode, languageCodeList } from "./languageCodes";

/**
 * Object of native names (label and changeLabel). Currently supported language codes are `en`, `fi` and `sv`.
 *
 * @returns object {label: string, changeLabel: string}
 */
export const languageNativeNames: Partial<
  { [key in LanguageCode]: { label: string; changeLabel: string } }
> = {
  en: { label: "English", changeLabel: "Change language" },
  fi: { label: "Suomi", changeLabel: "Vaihda kieltä" },
  sv: { label: "Svenska", changeLabel: "Ändra språk" },
};

/**
 * Get the current language code.
 */
export const getCurrentLanguageCode = (): LanguageCode =>
  (getLocalStorageValue(languageCodeLocalStorageKey) as LanguageCode) || "en";

/**
 * Set the current language code.
 * @param {LanguageCode} code
 */
export const setCurrentLanguageCode = (code: LanguageCode) => {
  // Set language code in local storage
  setLocalStorageValue(languageCodeLocalStorageKey, code);
  // set language in HTML Document
  document.documentElement.lang = code;
};

/**
 * Get a list of language codes for each language that is included in the locale (i18n).
 * @param i18n
 * @default ['en']
 */
export const getLocaleLanguageCodes = (i18n: i18n): Partial<LanguageCode>[] =>
  i18n.options.whitelist
    ? (i18n.options.whitelist.filter((code) =>
        languageCodeList.some((lCode) => code === lCode)
      ) as Partial<LanguageCode>[])
    : [languageCodes.English];

/**
 * Get all Init options required to initialize i18n in the app.
 */
export const getI18nInitOptions = ({
  resources,
  moduleIds,
  languageCodes = ["en"],
}: {
  resources: Resource;
  languageCodes?: LanguageCode[];
  moduleIds: string[];
}): InitOptions => {
  return {
    resources,
    ns: moduleIds,
    defaultNS: moduleIds[0],
    whitelist: languageCodes,
    fallbackLng: languageCodes[0],
    keySeparator: ".",
    interpolation: {
      escapeValue: false, // react already safes from xss
    },
  };
};

/**
 * Custom hook to get i18n's T-function for current Module
 */
export const useLocale = () => useModule().t;
