import { useState, useEffect } from "react";

/**
 * This hook allows you to easily detect when an element is visible on
 * the screen as well as specify how much of the element should be visible
 * before being considered on screen.
 * Perfect for lazy loading images or triggering animations when the user
 * has scrolled down to a particular section.
 * @param {React.RefObject<any>} targetRef - Target Element
 * @param {string} rootMargin - Size
 *
 * @author Gabe Ragland <> https://github.com/gragland
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 */
export function useOnScreen(
  targetRef: React.RefObject<any>,
  rootMargin = "0px"
) {
  // State and setter for storing whether element is visible
  const [isIntersecting, setIntersecting] = useState(false);

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        // Update our state when observer callback fires
        setIntersecting(entry.isIntersecting);
      },
      {
        rootMargin
      }
    );
    if (targetRef.current) {
      observer.observe(targetRef.current);
    }
    return () => {
      observer.unobserve(targetRef.current);
    };
  }, []); // Empty array ensures that effect is only run on mount and un-mount

  return isIntersecting;
}
