import React from "react";
import { Theme } from "@faharmony/theme";
import { useResponsive, useNetwork } from "@faharmony/helpers";
import { Grid, useToast } from "@faharmony/components";
import { useKeycloak } from "@faharmony/service";
import { GlobalBar } from "../GlobalBar";

import { IRenderProps } from "../types";
import { useCheckUserForRoles, combineModuleRoles } from "./helpers";

import AppNavigation from "./Navigation";
import MainView from "./MainView";
import SideDrawer, { setAppSideDrawer, resetAppSideDrawer } from "./SideDrawer";

const App = (props: IRenderProps) => {
  const { devMode, multilingual, Modules, _harmony } = props;
  const { keycloakInstance } = useKeycloak();
  const { isMobile } = useResponsive();
  const { addToast, removeToast } = useToast();
  const isAllowedAnything = useCheckUserForRoles(combineModuleRoles(Modules));

  useNetwork({
    onOffline: () =>
      addToast({
        id: "offline",
        title: "No internet connection",
        variant: "warning",
        persist: true,
      }),
    onOnline: () => removeToast("offline"),
  });

  return (
    <Grid
      spacing="none"
      gridTemplate={isMobile ? "48px auto / auto" : "auto / 52px auto"}
      overflow="hidden"
      style={{ background: Theme.colorBackground("base") }}
    >
      <GlobalBar {...{ devMode, multilingual, _harmony, keycloakInstance }} />
      <Grid
        overflow="hidden"
        gridTemplate={"auto / max-content auto max-content"}
        spacing="none"
        // animate
      >
        {/* App Navigation */}
        {isAllowedAnything ? (
          <AppNavigation {...props} />
        ) : (
          <div id="appNavigation" />
        )}

        {/* Primary View */}
        <MainView {...props} />

        {/* Side View */}
        <SideDrawer />
      </Grid>
    </Grid>
  );
};

export { setAppSideDrawer, resetAppSideDrawer };
export { useApp, AppContext } from "./context";
export default App;
