import { faQuestionCircle } from "@faharmony/icons";
import { IFABarProps, IGlobalBarProps } from "../types";
import useLanguageSwitcher from "./language";
import useUserAction from "./user";
import useThemeAction from "./theme";

const useStaticActions = (): IFABarProps["actions"] => {
  return [
    {
      id: "helpDesk",
      icon: faQuestionCircle,
      tooltip: "FA Help Desk",
      onClick: () =>
        window.open(`https://support.fasolutions.com/hc/en-us/`, "_blank"),
    },
  ];
};

export default ({
  multilingual,
  devMode = false,
  _harmony = false,
  accountUrl,
  logout,
}: IGlobalBarProps & {
  accountUrl: string;
  logout: () => void;
}): IFABarProps["actions"] => {
  const userActions = useUserAction(accountUrl, logout, devMode, _harmony);
  const themeAction = useThemeAction();
  const staticActions = useStaticActions();
  const languageSwitchActions = useLanguageSwitcher(multilingual);
  return [
    // Action to manage darkMode/theme
    themeAction,
    // Other actions without logic
    ...staticActions,
    // Add language switcher
    ...languageSwitchActions,
    // Add user action menu
    ...userActions,
  ];
};
