import React, { useMemo, useCallback, useState } from "react";
import { TippyProps } from "@tippyjs/react/headless";
import { styled } from "@faharmony/theme";
import { Instance } from "tippy.js";
import { motion, useSpring } from "@faharmony/helpers";
import { Theme } from "@faharmony/theme";
import { NOOP } from "@faharmony/helpers";
import { IPopoverProps, IPopoverInstance } from "./types";

const StyledPopover = styled(motion.div)`
  border: 1px solid ${() => Theme.colorOutline()};
  box-shadow: ${() => Theme.shadow("float")};
  border-radius: ${() => Theme.radius("sm")};
  background: ${() => Theme.colorBackground("elevated")};
  padding: 0;
  overflow: auto;
/* 
  #arrow,
  #arrow::before {
    position: absolute;
    width: 8px;
    height: 8px;
  }

  #arrow::before {
    content: "";
    transform: rotate(45deg);
    background: ${() => Theme.colorBackground("elevated")};
  }

  &[data-placement^="top"] > #arrow {
    bottom: -4px;
  }

  &[data-placement^="bottom"] > #arrow {
    top: -4px;
  }

  &[data-placement^="left"] > #arrow {
    right: -4px;
  }

  &[data-placement^="right"] > #arrow {
    left: -4px;
  } */
`;

const usePopoverOptions = ({
  onOpen = NOOP,
  onClose = NOOP,
  placement = "auto",
  persistent = false,
  trigger = "click",
  disabled = false,
  content,
  tippyProps,
}: IPopoverProps) => {
  const [instance, setInstance] = useState<IPopoverInstance>();

  const initialScale = 0.9;
  const opacity = useSpring(0, Theme.transition());
  const scale = useSpring(initialScale, Theme.transition());

  const onCreate = (tippyInstance: Instance<any>) => {
    setInstance({
      closePopover: tippyInstance.hide,
      id: tippyInstance.id.toString(),
      openPopover: tippyInstance.show,
    });
  };

  const onMount = useCallback(() => {
    scale.set(1);
    opacity.set(1);
    onOpen();
  }, []);

  const onHide = useCallback(({ unmount }: Instance<any>) => {
    const cleanup = scale.onChange((value) => {
      if (value <= initialScale) {
        cleanup();
        unmount();
      }
    });
    scale.set(initialScale);
    opacity.set(0);
    onClose();
  }, []);

  const render = useCallback(
    (attrs) =>
      instance ? (
        <StyledPopover
          className="popover"
          tabIndex={-1}
          role="popover"
          style={{ scale, opacity }}
          {...attrs}
        >
          {content(instance)}
          {/* <div id="arrow" data-popper-arrow></div> */}
        </StyledPopover>
      ) : null,
    [instance, scale, opacity, content]
  );

  const isContextMenu = trigger === "contextmenu";
  const options: TippyProps = useMemo(
    () => ({
      allowHTML: true,
      appendTo: () => document.body,
      arrow: true,
      placement,
      render,
      hideOnClick: persistent ? "toggle" : true,
      interactive: true,
      offset: [0, 4],
      onCreate,
      onMount,
      onHide,
      disabled,
      touch: isContextMenu ? "hold" : true,
      trigger,
      zIndex: Theme.zIndex("popover"),
      ...tippyProps,
    }),
    [render, tippyProps, isContextMenu, trigger, onMount, onHide]
  );
  return { options, instance };
};

export default usePopoverOptions;
