import React from "react";
import { Theme, getTextDimensions } from "@faharmony/theme";
import { Box, Icon, Text, Tooltip } from "@faharmony/components";
import { IBreadcrumb, IBreadcrumbsProps } from "./types";

const maxCrumbWidth = 200;

const Crumb = ({
  crumb: { id, icon, label, onClick, tooltip },
  lastCrumb,
  variant,
  separator,
}: {
  crumb: IBreadcrumb;
  lastCrumb: boolean;
  separator: string;
  variant: IBreadcrumbsProps["variant"];
}) => {
  const showTooltip = tooltip
    ? true
    : label
    ? getTextDimensions(label, variant).width > maxCrumbWidth - 20
    : false;

  if (icon || label) {
    const item = (
      <Box
        onClick={onClick}
        width="auto"
        role="breadcrumb"
        style={{
          color: onClick
            ? Theme.colorText("secondary")
            : lastCrumb
            ? Theme.colorText("primary")
            : Theme.colorText("disabled"),
          cursor: onClick ? "pointer" : "initial",
          maxWidth: maxCrumbWidth + "px",
        }}
        spacing="sm"
      >
        {icon && <Icon icon={icon} />}
        {label && <Text value={label} variant={variant} truncate />}
        {(!lastCrumb || onClick) && (
          <Text value={` ${separator} `} variant={variant} color="disabled" />
        )}
      </Box>
    );
    if (showTooltip)
      return <Tooltip value={tooltip || label || ""}>{item}</Tooltip>;
    else return item;
  }
  console.warn(
    `Crumb ${id} could not be rendered since there is no label or icon.`
  );
  return null;
};

export default Crumb;
