import { styled } from "@faharmony/theme";
import { Theme } from "@faharmony/theme";

interface ISkeletonProps {
  /**
   * Height of the skeleton
   * @default '100%'
   */
  height?: React.CSSProperties["height"];
  /**
   * Width of the Skeleton
   * @default '100%'
   */
  width?: React.CSSProperties["width"];
  /**
   * Duration of single animation cycle (in seconds)
   * @default 5
   */
  duration?: number;
}

/**
 * Skeleton
 * ---
 * Display a placeholder preview of your content before the data gets loaded to reduce load-time frustration.
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @version 1.5.0
 *
 * @example
 * <Skeleton width="200px" height="20px" />
 */
const Skeleton = styled.div<ISkeletonProps>`
  height: ${(p) => p.height || "100%"};
  width: ${(p) => p.width || "100%"};

  animation-duration: ${(p) => p.duration || 5}s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
  animation-name: placeHolderShimmer;
  animation-timing-function: linear;

  background: ${Theme.props.colorBackground.elevated};
  background: linear-gradient(
    to right,
    ${Theme.props.colorBackground.disabled} 8%,
    ${Theme.props.colorBackground.base} 30%,
    ${Theme.props.colorBackground.disabled} 60%
  );
  border-radius: ${Theme.props.radius.sm};

  @keyframes placeHolderShimmer {
    0% {
      background-position: 0px 0;
    }
    100% {
      background-position: 100em 0;
    }
  }
`;

export { Skeleton, ISkeletonProps };
