import React from "react";
import { Theme } from "@faharmony/theme";
import { Grid } from "@faharmony/components";

interface ILayoutProps {
  /**
   * Primary or permanent area
   */
  primaryNode: React.ReactNode;
  /**
   * Secondary or temporary area
   */
  secondaryNode: React.ReactNode;
  /**
   * Max width of secondary node.
   * @default '300px'
   */
  secondaryNodeWidth?: React.CSSProperties["maxWidth"];
}

/**
 * PrimaryWithSidebarLayout
 * ---
 * It is used to split the available space in two. The primary part of layout is supposedly always visible. The secondary part of layout is expected to be temporary.
 * @example
 * <PrimaryWithSidebarLayout primaryNode={<div />} secondaryNode={<div />} />
 */
export const PrimaryWithSidebarLayout = (props: ILayoutProps) => (
  <Grid
    width="100%"
    height="100%"
    gridTemplate="100% / 1fr max-content"
    spacing="none"
    overflow="visible"
  >
    {props.primaryNode}
    <div
      style={{
        zIndex: Theme.zIndex("docked"),
        boxShadow: Theme.shadow("float"),
        overflow: "hidden",
        height: `calc(100% + ${Theme.spacing("xl")})`,
        maxWidth: props.secondaryNodeWidth || "300px",
        transform: `translateX(${Theme.spacing()}) translateY(calc(-1 * ${Theme.spacing()}))`,
        backgroundColor: Theme.colorBackground("elevated"),
      }}
    >
      {props.secondaryNode}
    </div>
  </Grid>
);
