import React from "react";
import { i18nProps, i18n } from "@faharmony/locale";
import { useResponsive } from "@faharmony/helpers";
import { IModuleProps, ISubModuleProps } from "@faharmony/module";
import {
  useHistory,
  Menu,
  IMenuProps,
  IMenuSecondaryItem,
} from "@faharmony/router";

import { IRenderProps } from "../types";
import { useCheckUserForRolesCallback } from "./helpers";

const useMenu = (Modules: IModuleProps[], i18next?: i18nProps) => {
  const history = useHistory();
  const checkRoles = useCheckUserForRolesCallback();
  let menu: IMenuProps["menu"] = [];
  let menuAlt: IMenuProps["menu"] = [];

  const generateSecondaryItem = (
    subModule: ISubModuleProps,
    Module: IModuleProps
  ): IMenuSecondaryItem | undefined => {
    const {
      id,
      label,
      icon,
      trailingIcon,
      trailingIconTint,
      featured,
    } = subModule;
    const subModuleLocation = `/${Module.id}/${id}`;
    const item: IMenuSecondaryItem = {
      id,
      label:
        i18n.exists(`${Module.id}:${label}`) && i18next
          ? i18next.t(`${Module.id}:${label}`)
          : label,
      icon,
      trailingIcon,
      trailingIconTint,
      handleClick: () => history.push(subModuleLocation),
      isSelectedCallback: () => {
        const locSplit = window.location.hash.split(/\/(?=.)/);
        return locSplit.length > 2 && locSplit[2].startsWith(subModule.id);
      },
    };
    if (featured) {
      // If subModule is feature, add it as primary item.
      menuAlt.push(item);
      return undefined;
    } else return item;
  };

  Modules.forEach((Module) => {
    const { id, label, icon, roles, subModules } = Module;
    const moduleLocation = `/${id}`;
    if (checkRoles(roles))
      menu.push({
        id,
        label:
          i18n?.exists(`${id}:moduleName`) && i18next
            ? i18next.t(`${Module.id}:moduleName`)
            : label,
        icon,
        handleClick: () => history.push(moduleLocation),
        isSelectedCallback: () => {
          const locSplit = window.location.hash.split(/\/(?=.)/);
          return (
            locSplit.length === 2 && locSplit[1].startsWith(id)
            // locSplit.length > 1 && locSplit.length < 3 && locSplit[1].startsWith(id)
          );
        },
        subItems: subModules?.reduce((list, subModule) => {
          const item = generateSecondaryItem(subModule, Module);
          if (item) list.push(item);
          return list;
        }, [] as any),
      });
    // Combine modules with featured subModules
    menu = [...menu, ...menuAlt];
    // Clean variable for next run
    menuAlt = [];
  });

  return menu;
};

export const AppNavigation = ({
  Modules,
  appConfig,
  multilingual,
  enableNavigationFilter,
}: IRenderProps) => {
  const menu = useMenu(Modules, multilingual);
  const { isMobile } = useResponsive();
  return (
    <Menu
      heading={appConfig.name}
      shortHeading={appConfig.shortName}
      descriptionText={"version " + appConfig.version}
      menu={menu}
      actions={appConfig.actions}
      isMenuCollapsedDefault={menu.length === 0}
      isHeaderVisible={!isMobile}
      enableMenuFilter={enableNavigationFilter}
    />
  );
};

export default AppNavigation;
