import { v4 as uuid } from "uuid";
import { EventEmitter } from "events";
import { useEffect } from "react";

type ExecuteCallback<T> = (payload: T) => void;

/**
 * * Trigger
 * ---
 * Pub/Sub Implementation of Node EventEmitter
 * Create a trigger which can be used in other parts of app.
 *
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @example
 * // TS
 * // PayloadType is the type of data that is sent and received by the trigger.
 * type PayloadType = string;
 * export const myTrigger = new Trigger<PayloadType>();
 *
 * // JS
 * export const myTrigger = new Trigger();
 *
 * ------
 * myTrigger.publish("Item");
 * ------
 * // Hook
 * myTrigger.useSubscribe(payload => console.log(payload));
 * ------
 * // Non-hook
 * myTrigger.subscribe(console.log);
 * myTrigger.unsubscribe(console.log);
 */
class Trigger<T = any> {
  private eventName: string;
  private eventEmitter: EventEmitter;
  /**
   * Initiate the event emitter
   */
  constructor() {
    this.eventEmitter = new EventEmitter();
    this.eventName = uuid();
  }
  /**
   * Publish (set) new payload to the trigger
   * @param {object, T} payload
   * @example
   * myTrigger.publish("Item");
   */
  publish = (payload: T) => {
    this.eventEmitter.emit(this.eventName, payload);
  };
  /**
   * Subscribe and listen to changes to a trigger and execute a function.
   * Needs to be unsubscribed. Otherwise use useSubscribe.
   * @param {Function} execute Function to execute (receives payload as argument)
   */
  subscribe = (execute: ExecuteCallback<T>) => {
    this.eventEmitter.addListener(this.eventName, execute);
    return this.unsubscribe;
  };
  /**
   * Will remove the specified execute function. The execute callback should be exact same as passed while subscribing.
   * @param {function} execute
   */
  unsubscribe = (execute: ExecuteCallback<T>) => {
    this.eventEmitter.removeListener(this.eventName, execute);
  };
  /**
   * Listen once
   */
  subscribeOnce = (execute: ExecuteCallback<T>) => {
    this.eventEmitter.once(this.eventName, execute);
  };
  /**
   * Hook to Subscribe and listen to changes and execute a function.
   * It manages subscriptions and un-subscription.
   * Can only be used inside component or other Hook.
   * @param {Function} execute Function to execute (receives payload as argument)
   * @example
   * myTrigger.useSubscribe((payload) => console.log(payload));
   */
  useSubscribe = (execute: ExecuteCallback<T>) =>
    useEffect(() => {
      this.subscribe(execute);
      return () => {
        this.unsubscribe(execute);
      };
    }, []);
}

export { Trigger };
export default Trigger;
