import { styled, Theme, getTextDimensions, SMLType } from "@faharmony/theme";

import { _IInputProps, IInputFieldContainerProps } from "./types";

const commonClass = "InputField";
const inputHeight = 20;

const calcHeight = ({ spacing }: IInputFieldContainerProps) => {
  const baseHeight = 32;
  const height =
    (spacing &&
      (["lg", "xl"].includes(spacing)
        ? baseHeight + 4
        : ["sm", "xs"].includes(spacing)
        ? baseHeight - 4
        : ["none"].includes(spacing)
        ? inputHeight
        : baseHeight)) ||
    baseHeight;
  return height;
};

const prefixTextWidth = (text: string | undefined, spacing: SMLType = "md") => {
  if (!text) return 0;
  const height = calcHeight({ spacing });
  const width = getTextDimensions(text, "p").width;
  if (width < height) return height;
  else return width + 10;
};

const calcPrefixWidth = ({ prefixIcon, prefixText, spacing }: _IInputProps) =>
  prefixIcon ? calcHeight({ spacing }) : prefixTextWidth(prefixText, spacing);

const StyledInputFieldContainer = styled.div<IInputFieldContainerProps>`
  height: ${calcHeight}px;
  width: 100%;
  border-radius: ${() => Theme.radius("sm")};
  border: 1px solid ${() => Theme.colorOutline()};
  background: ${(p) =>
    Theme.colorBackground(p.disabled || p.readonly ? "disabled" : "input")};
  transition: border 0.5s;
  display: flex;
  color: ${() => Theme.colorText("secondary")};
  overflow: hidden;

  .prefix {
    max-width: 100px;
    min-width: 0px;
    height: 100%;
    width: ${calcPrefixWidth}px;
    padding: 0 ${(p) => (p.prefixText ? Theme.spacing("sm") : 0)};
    padding-top: ${(p) =>
      (calcHeight(p) - inputHeight - (p.prefixIcon ? 2 : 0)) / 2}px;
    text-align: center;
    position: relative;
    color: inherit;
    background: ${(p) =>
      Theme.colorBackground(p.disabled || p.readonly ? "disabled" : "base")};
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .inputArea {
    width: 100%;
    height: 100%;
    position: relative;

    .input {
      height: ${inputHeight}px;
      border: none;
      outline: none;
      background: transparent;

      position: absolute;
      left: ${() => Theme.spacing("sm")};
      top: 50%;
      transform: translateY(-50%);
      width: calc(100% - ${(p) => (p.isClearable ? "30px" : "8px")});
      color: inherit;

      :focus {
        color: ${() => Theme.colorText("primary")};
      }

      :read-only {
        color: ${() => Theme.colorText("secondary")};
      }

      :disabled {
        color: ${() => Theme.colorText("disabled")};
      }

      ::placeholder {
        color: ${() => Theme.colorText("disabled")};
      }
    }

    .clearButton {
      position: absolute;
      right: ${() => Theme.spacing("sm")};
      top: 50%;
      transform: translateY(-50%);
      color: ${() => Theme.colorText("disabled")};
      cursor: pointer;
      transition: color 0.5s;
      :hover {
        color: ${() => Theme.colorText("primary")};
      }
    }
  }

  :hover,
  :focus {
    border-color: ${(p) =>
      p.disabled ? "none" : Theme.colorBackground("inverse")};
  }
`;

export { commonClass, calcPrefixWidth, StyledInputFieldContainer };
