import { CSSProperties } from "react";
import { styled } from "@faharmony/theme";
import { Theme, SMLType } from "@faharmony/theme";

interface IGridProps {
  /**
   * Width of Grid
   * @default '100%'
   */
  width?: CSSProperties["width"];
  /**
   * Height of Grid
   * @default '100%'
   */
  height?: CSSProperties["height"];
  /**
   * Spacing between children
   * @default 'md'
   */
  spacing?: SMLType;
  /**
   * Overflow
   * @default 'auto'
   */
  overflow?:
    | "auto"
    | "visible"
    | "scroll"
    | "hidden"
    | "clip"
    | "inherit"
    | "initial"
    | "unset";

  /**
   * Specifies the size of an implicitly-created grid row track or pattern of tracks.
   * @default auto
   */
  gridAutoRows?: CSSProperties["gridAutoRows"];
  /**
   * Specifies the size of an implicitly-created grid column track or pattern of tracks.
   * @default auto
   */
  gridAutoColumns?: CSSProperties["gridAutoColumns"];
  /**
   * Direction of placement => row or column
   * @default row
   */
  direction?: CSSProperties["gridAutoFlow"];
  /**
   * Shorthand property for defining grid columns, rows, and areas.
   * @default auto
   */
  gridTemplate?: CSSProperties["gridTemplate"];
}

/**
 * Grid
 * ---
 * Grid layout
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @version 1.5.0
 * @example
 * <Grid>{Other stuff...}</Grid>
 */
const Grid = styled.div<IGridProps>`
  position: relative;
  display: grid;
  gap: ${(props) => Theme.spacing(props.spacing || "md")};
  width: ${(props) => props.width || "100%"};
  height: ${(props) => props.height || "100%"};
  overflow: ${(props) => props.overflow || "auto"};
  grid-template: ${(props) => props.gridTemplate || "none"};
  grid-auto-flow: ${(props) => props.direction || "row"};
  grid-auto-rows: ${(props) => props.gridAutoRows || "auto"};
  grid-auto-columns: ${(props) => props.gridAutoColumns || "auto"};
`;

export { Grid, IGridProps };
