import React from "react";

import { useResponsive } from "@faharmony/helpers";
import { styled, Theme } from "@faharmony/theme";
import { Text, Button, Divider, Box } from "@faharmony/components";
// keyPressCombos,
// getKeyPressComboString,
import {
  faChevronSquareLeft,
  faChevronSquareRight,
  faFilter,
  faTimesCircle,
} from "@faharmony/icons";
import { useVisibleText } from "./helpers";
import { INavFooterProps } from "./types";

const StyledFooter = styled.footer<INavFooterProps>`
  width: inherit;
  background: transparent;
  overflow: hidden;
  padding: ${(props) =>
    `0 ${Theme.spacing(
      props.isMenuCollapsed ? "sm" : "md"
    )} ${Theme.spacing()}`};
  display: flex;
  flex-direction: column;
  justify-content: flex-end;

  .menuCollapsedHeading {
    height: auto;
    overflow: hidden;
    writing-mode: vertical-rl;
    transform: rotate(180deg);
    justify-self: center;
    margin: ${() => Theme.spacing()} 0;
  }

  > .NavFooter-Actions {
    margin-top: ${() => Theme.spacing()};
    padding-left: ${(props) =>
      props.isMenuCollapsed
        ? `calc(${Theme.spacing()} - ${Theme.spacing("xs")})`
        : 0};
  }

  #navFilterInput {
    border: 1px solid ${() => Theme.colorOutline()};
    border-radius: ${() => Theme.radius("sm")};
    height: 28px;
    width: 100%;
    background: ${() => Theme.colorBackground("base")};
    padding: 0 ${() => Theme.spacing()};
    color: ${() => Theme.colorText()};
  }
`;

/**
 * Navigation Footer
 * ---
 * Builds the Footer containing
 * CollapseButton and FilterButton
 *
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 */
const Footer = (props: INavFooterProps) => {
  const {
    descriptionText,
    isMenuCollapsed,
    toggleMenuCollapse,
    handleFiltering,
    enableMenuFilter = false,
  } = props;
  const { isMobile } = useResponsive();
  const ref = React.useRef<HTMLDivElement>(null);
  const { isHidden, visibleText } = useVisibleText(
    ref,
    isMenuCollapsed,
    props.heading,
    props.shortHeading
  );
  const [isFiltering, setIsFiltering] = React.useState(false);
  return (
    <StyledFooter {...props} ref={ref}>
      {(!isMenuCollapsed || isHidden) && <Divider spacing="none" />}
      {isMenuCollapsed && (
        <div className="menuCollapsedHeading">
          {isMobile ? (
            <div />
          ) : (
            <Text value={visibleText} variant="h1" truncate />
          )}
        </div>
      )}
      <Box
        justifyContent="space-between"
        wrap="nowrap"
        className="NavFooter-Actions"
      >
        <Button
          key="CollapseMenu"
          icon={isMenuCollapsed ? faChevronSquareRight : faChevronSquareLeft}
          // tooltip={`${
          //   isMenuCollapsed ? "Expand" : "Collapse"
          // } [${getKeyPressComboString(keyPressCombos.collapseNavigationMenu)}]`}
          tooltip={`${isMenuCollapsed ? "Expand" : "Collapse"}`}
          spacing="sm"
          onClick={toggleMenuCollapse}
        />

        {!isMenuCollapsed && !isFiltering && descriptionText && (
          <Text
            variant="small"
            color="disabled"
            value={descriptionText}
            align="center"
          />
        )}

        {enableMenuFilter ? (
          !isMenuCollapsed &&
          (isFiltering ? (
            <React.Fragment>
              <input
                id="navFilterInput"
                onChange={(e) => {
                  const val = e.target.value;
                  handleFiltering(val);
                }}
                placeholder="Filter navigation menu"
                autoFocus
              />
              <Button
                key="CloseFilter"
                icon={faTimesCircle}
                tooltip={"Close filter"}
                spacing="sm"
                onClick={() => {
                  setIsFiltering(false);
                  handleFiltering("");
                }}
              />
            </React.Fragment>
          ) : (
            <Button
              key="FilterMenu"
              icon={faFilter}
              tooltip={"Filter menu items"}
              spacing="sm"
              onClick={() => setIsFiltering(true)}
            />
          ))
        ) : (
          <div style={{ width: "28px" }} />
        )}
      </Box>
    </StyledFooter>
  );
};

export default Footer;
