import WebFont from "webfontloader";
import { SMLType, IThemeColorsProps, IThemeProps } from "./types";
import {
  getColorTint,
  zIndices,
  shadows,
  transitions,
  getFontName,
  getProps,
} from "./utilities";
import { getCurrentDarkModePreference } from "./helpers";
import { generateGlobalCSS, generateThemeCSS } from "./css";
import defaultTheme from "./defaultTheme";

/**
 * Theme
 * ---
 * This object provides multiple callbacks for different parts of the theme.
 */
const Theme = {
  // Radius
  radius: (type: SMLType = "md") => `var(--radius-${type})`,

  // Spacing
  spacing: (type: SMLType = "md") => `var(--spacing-${type})`,

  // zIndex
  zIndex: (type: keyof typeof zIndices = "base") => zIndices[type],

  // Shadows
  shadow: (type: keyof typeof shadows = "default") => shadows[type],

  // Transition
  transition: (type: keyof typeof transitions = "spring") => transitions[type],

  // Colors
  colorText: (type: keyof IThemeColorsProps["text"] = "primary") =>
    `var(--colors-text-${type})`,

  colorBackground: (type: keyof IThemeColorsProps["background"] = "base") =>
    `var(--colors-background-${type})`,

  colorOutline: (type: keyof IThemeColorsProps["outline"] = "border") =>
    `var(--colors-outline-${type})`,

  colorTint: getColorTint,

  fontName: () => getFontName(defaultTheme.text),

  // Get all theme properties without executing function.
  props: getProps(),
} as const;

/**
 * Function to set or update theme, and dark mode.
 * The function writes global/root CSS which then can be accessed by any component.
 */
function setTheme({
  theme = defaultTheme,
  enableDarkMode = getCurrentDarkModePreference(),
  globalCSS = "",
}: {
  theme?: IThemeProps;
  enableDarkMode?: boolean;
  globalCSS?: string;
}) {
  // Load fonts
  WebFont.load(theme.text.fontConfig);
  WebFont.load(theme.code.fontConfig);

  // Add global CSS
  generateThemeCSS(theme, enableDarkMode);
  generateGlobalCSS(Theme, globalCSS);
}

export { Theme, setTheme };
