import React from "react";
import { styled } from "@faharmony/theme";
import { Theme } from "@faharmony/theme";
import { Text } from "../Text";

interface IPreloaderProps {
  /**
   * Place any component below loading text
   */
  content?: React.ReactNode;
}

const StyledLoader = styled.div`
  position: relative;
  width: 100%;
  height: 100%;
  min-height: 160px;
  background-color: ${() => Theme.colorTint("black", 10)};

  .Loader {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    height: 100px;
    width: 100px;

    h4 {
      position: absolute;
      width: 100px;
      bottom: 4px;
    }

    .inner {
      position: relative;
      display: block;

      .Square {
        /* position: absolute; */
        animation: loader 3s infinite ease;
        width: 100px;
        height: 100px;
        background: ${() => Theme.colorBackground("elevated")};
        border-radius: 20px;
        margin: 0 auto;
        box-shadow: ${() => Theme.shadow()};
      }

      .FA {
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
      }

      @keyframes loader {
        0% {
          transform: rotate(0deg);
        }

        25% {
          transform: rotate(190deg);
        }

        50% {
          transform: rotate(180deg);
        }

        75% {
          transform: rotate(370deg);
        }

        100% {
          transform: rotate(360deg);
        }
      }
    }
  }
`;

