import React from "react";
import { useCombinedRef } from "@faharmony/helpers";

import { _IInputProps, ChangeEventType, IUseInputFieldProps } from "./types";

const useInputField = ({
  defaultValue,
  ref,
  onChange,
  onInputClear,
}: IUseInputFieldProps) => {
  // Refs
  const inputRef = React.useRef<HTMLInputElement>(null);
  const targetInputRef = useCombinedRef(ref, inputRef);

  // currentValue
  const [currentValue, setCurrentValue] = React.useState<string>(
    defaultValue?.toString() || ""
  );
  const handleInputChange = React.useCallback(
    (e: ChangeEventType) => {
      setCurrentValue(e.target.value);
      onChange && onChange(e);
    },
    [onChange]
  );

  // clearButton
  const [isClearVisible, setClearVisible] = React.useState<boolean>(
    currentValue.toString().length > 0
  );
  React.useEffect(() => {
    setClearVisible(currentValue.length > 0);
  }, [currentValue]);
  const handleInputClear = React.useCallback(() => {
    setCurrentValue("");
    onInputClear && onInputClear();
  }, []);

  return {
    targetInputRef,
    currentValue,
    isClearVisible,
    handleInputChange,
    handleInputClear,
  };
};

export { useInputField };
