import { styled, Theme } from "@faharmony/theme";
import { headerHeight } from "../common";

interface Props {
  hasActionArea: boolean;
  hasFooter: boolean;
}

export const StyledView = styled.aside<Props>`
  margin: 0;
  height: 100%;
  max-height: 100vh;
  width: 100%;
  max-width: 100vw;
  min-width: 240px;
  padding: ${() => Theme.spacing()};
  display: grid;
  gap: ${() => Theme.spacing()};
  grid-template-rows: max-content ${(p) =>
    p.hasActionArea && "max-content"} 1fr ${(p) =>
  p.hasFooter && "max-content"};
  overflow: hidden;
  /* background-color: ${() => Theme.colorBackground("base")}; */
  background-color: transparent;
  /* box-shadow: ${() => Theme.shadow("float")}; */
  z-index: ${() => Theme.zIndex("docked")};
`;

export const StyledHead = styled.header`
  width: 100%;
  height: ${headerHeight - 8}px;
  max-height: ${headerHeight - 8}px;
  padding: 0 ${() => Theme.spacing()};
  overflow: visible;
  display: grid;
  gap: ${() => Theme.spacing("sm")};
  grid-template-rows: auto max-content;

  #summaryView-header-title {
    overflow: hidden;

    .TextContainer {
      margin-bottom: ${() => Theme.spacing("sm")};
    }

    .h3 {
      word-break: break-word;
    }
  }

  .headerDivider {
    margin: 0 calc(-1 * ${Theme.props.spacing.md});
  }
`;

export const StyledBody = styled.div`
  position: relative;
  width: 100%;
  max-height: 100%;
  overflow: auto;

  #summaryView-noContent {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
  }
`;

export const StyledFooter = styled.footer`
  width: 100%;
  height: auto;
  padding: 0 ${() => Theme.spacing()};
  display: grid;
  gap: ${() => Theme.spacing()};
  overflow: hidden;
  grid-template-rows: repeat(2, max-content);
`;
