import React from "react";
import { Theme, styled } from "@faharmony/theme";
import { Tooltip, Icon } from "@faharmony/components";
import { FALogoOpaqueIconDefinition } from "@faharmony/icons";

interface IFAGlobalBarLogo {
  horizontal?: boolean;
}

const StyledLogo = styled.div<IFAGlobalBarLogo>`
  margin: 0;
  width: 100%;
  height: 100%;
  background-color: transparent;
  text-align: center;
  color: ${() => Theme.colorTint()};
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 36px;

  .faLogo {
    ${(p) =>
      !p.horizontal && `transform: translateY(${Theme.props.spacing.sm});`};
    --fa-primary-color: #ffffff;
    --fa-primary-opacity: 1;
    --fa-secondary-color: #ae001a;
    --fa-secondary-opacity: 1;
  }
`;

const FALogo = ({ horizontal = false }: IFAGlobalBarLogo) => {
  return (
    <StyledLogo
      onClick={() => window.location.reload()}
      horizontal={horizontal}
    >
      <Tooltip value="FA Platform">
        <div className="faLogo">
          <Icon size="1x" icon={FALogoOpaqueIconDefinition} />
        </div>
      </Tooltip>
    </StyledLogo>
  );
};

export default FALogo;
