import { IFABarProps } from "../types";
import { faApps, harmonyApp } from "./apps";

export default (
  clients: string[] = [],
  isAdmin: boolean = false,
  devMode: boolean = false,
  _harmony: boolean = false
): IFABarProps["apps"] => {
  // If app is running in devMode
  if (_harmony) return harmonyApp;

  // Return all apps id user is Admin
  if (isAdmin || devMode) return faApps;

  // Return selected apps to in which user has at least one role.
  const apps: IFABarProps["apps"] = [];
  faApps.forEach((app) => {
    if (clients.some((client) => client === (app.keycloakClient || app.id)))
      apps.push(app);
  });
  return apps;
};

export { faApps };
