import { IThemeProps, IThemeTextStyleProps, isThemeTextStyle } from "./types";
import { setCurrentDarkModePreference } from "./helpers";
import { getFontName } from "./utilities";

export const generateColorsCSS = (
  theme: IThemeProps,
  enableDarkMode: boolean
) => {
  function convertHex(hex: string, opacity: number) {
    hex = hex.replace("#", "");
    const r = parseInt(hex.substring(0, 2), 16);
    const g = parseInt(hex.substring(2, 4), 16);
    const b = parseInt(hex.substring(4, 6), 16);
    return "rgba(" + r + "," + g + "," + b + "," + opacity / 100 + ")";
  }
  const colors =
    enableDarkMode && theme.darkColors ? theme.darkColors : theme.lightColors;
  let cssString = "";
  for (const [key1, value1] of Object.entries({ ...colors }))
    for (const [key2, value2] of Object.entries({ ...value1 }))
      if (key1 === "tint")
        for (let opacity = 100; opacity >= 0; opacity -= 10)
          cssString += `--colors-${key1}-${key2}-${opacity.toString()}:${convertHex(
            value2,
            opacity
          )};`;
      else cssString += `--colors-${key1}-${key2}:${value2};`;
  return cssString;
};

export const generateSMLType_CSS = (
  type: string,
  base: number,
  size: IThemeProps["sizeMultiplier"]
) => {
  let cssString = "";
  cssString += `--${type}: ${base.toString()}px;`;
  for (const [key, value] of Object.entries({ ...size }))
    cssString += `--${type}-${key}: ${(base * value).toString()}px;`;
  cssString += `--${type}-none: ${(base * 0).toString()}px;`;
  return cssString;
};

export const generateFontCSS = (theme: IThemeProps) => {
  function fontCSSString(name: string, style: IThemeTextStyleProps) {
    return `{       
      font-family: ${name};
      font-style: normal;
      font-size: ${style.fontSize}; 
      font-weight: ${style.fontWeight}; 
      line-height: ${style.lineHeight}; 
      letter-spacing: ${style.letterSpacing}; 
      text-transform: ${style.textTransform}; 
      margin: 0;
      vertical-align: bottom;
      white-space: wrap;
      display: inline;
    }`;
  }

  let cssString = "";
  Object.entries(theme.text).forEach(([key, val]) => {
    if (isThemeTextStyle(val))
      cssString += `${key}, .${key} ${fontCSSString(
        getFontName(theme.text),
        val
      )}`;
  });
  cssString += `code, .code ${fontCSSString(
    getFontName(theme.code),
    theme.code.style
  )}`;

  return cssString;
};

export const generateThemeCSS = (
  theme: IThemeProps,
  enableDarkMode: boolean
) => {
  // cssString contains the variables-value pairs
  let cssString = `:root {`;

  // Set DarkMode globally
  setCurrentDarkModePreference(enableDarkMode);
  cssString += `--darkmode: ${enableDarkMode.toString()};`;

  // Colors
  cssString += generateColorsCSS(theme, enableDarkMode);

  // Radius
  cssString += generateSMLType_CSS(
    "radius",
    theme.baseRadius,
    theme.sizeMultiplier
  );

  // Spacing
  cssString += generateSMLType_CSS(
    "spacing",
    theme.baseSpacing,
    theme.sizeMultiplier
  );

  // Close ROOT
  cssString += `} `;

  // Text
  cssString += generateFontCSS(theme);

  // Place it in CSS
  const styleTag = document.createElement("style");
  styleTag.innerHTML = cssString;
  document.head.insertAdjacentElement("beforeend", styleTag);
};

export const generateGlobalCSS = (Theme: any, globalCss: string) => {
  const styleTag = document.createElement("style");
  styleTag.innerHTML = `
       :root {
            background: white;
          }
  
          html {
            box-sizing: border-box;
          }
  
          
          /* width */
          *::-webkit-scrollbar,
          *::-webkit-scrollbar:horizontal,
          *::-webkit-scrollbar:vertical {
            width: ${Theme.spacing("sm")};
            height: ${Theme.spacing("sm")};
          }

          /* Track & corner */
          *::-webkit-scrollbar-track,
          *::-webkit-scrollbar-corner {
            /* box-shadow: inset 0 0 5px grey; */
            background: ${Theme.colorBackground("hover")};
          }

          /* Handle */
          *::-webkit-scrollbar-thumb {
            background: ${Theme.colorText("disabled")};
            border-radius: 10px;
          }

          /* Handle on hover */
          *::-webkit-scrollbar-thumb:hover {
            background: ${Theme.colorOutline("shadowHover")};
          }
          
  
          *,
          *:before,
          *:after {
            box-sizing: inherit;
            scrollbar-color: var(--themeMode);
            scrollbar-width: thin;
          }
  
          *:focus {
            outline: none !important;
          }
  
          html,
          body {
            margin: 0;
            padding: 0;
            width: 100vw;
            height: 100vh;
            font-family: Muli, sans-serif;
            font-size: 14px;
            color: ${Theme.colorText()};
            overflow: hidden;
          }
  
          .noselect {
            -webkit-touch-callout: none;
            -webkit-user-select: none;
            -khtml-user-select: none;
            -moz-user-select: none;
            -ms-user-select: none;
            user-select: none;
          }

          .stretchHeight {
            height: 100%;
            height: -moz-available;          /* Mozilla */
            height: -webkit-fill-available;  /* Safari, Chrome */
            height: stretch;
          }
  
          pre {
            font-family: Fira Code, monospace; 
            background: ${Theme.colorBackground("disabled")};
            padding: ${Theme.spacing()};
            border-radius: ${Theme.radius("sm")};
            overflow-x: auto;
          }        

    
          code, .code {
            background: var(--colors-background-disabled);
            padding: 0 2px;
            margin: 0 2px;
          }

          a, a:link, a:visited, a:active {
            color: ${Theme.colorTint("blue")};
          }

          input {
            font-family: Muli, sans-serif;
            font-size: 14px;
            color: ${Theme.colorText()};
          }

          blockquote {
            font-style: italic !important;
            opacity: 0.9 !important;
          }
  

          // Tippy
          .tippy-iOS {
            cursor: pointer !important;
            -webkit-tap-highlight-color: transparent;
          }
  
      ${globalCss}
    `;
  document.head.insertAdjacentElement("beforeend", styleTag);
};
