import React from "react";
import { faChevronDown, faChevronRight } from "@faharmony/icons";
import { OnClickEventType } from "@faharmony/helpers";
import {
  Text,
  Icon,
  Button,
  Popover,
  IPopoverMenuItemProps,
} from "@faharmony/components";

import { useExpandItem, useSubItemSelected } from "../helpers";
import { IMenuPrimaryItemProps } from "../types";
import { StyledNavPrimaryItem } from "./Style";
import SecondaryItem from "./SecondaryItem";

const CollapsedMenuNavButton = (props: IMenuPrimaryItemProps) => {
  const { item, isSelected, selectedId, handleItemSelect } = props;
  const subItemSelected = useSubItemSelected(item, selectedId);
  const buttonLabel = item.icon ? undefined : item.label[0].toUpperCase();
  const buttonVariant = isSelected || subItemSelected ? "primary" : "secondary";
  const handleClick = (e?: OnClickEventType) => {
    e?.preventDefault();
    e?.stopPropagation();
    item.handleClick && item.handleClick(e);
    handleItemSelect(item);
  };
  const button = (
    <Button
      onClick={handleClick}
      icon={item.icon}
      tooltip={item.label}
      value={buttonLabel}
      variant={buttonVariant}
    />
  );

  return item.subItems?.length ? (
    <Popover.Menu
      placement="right"
      trigger="contextmenu"
      menuItems={[
        {
          id: item.id,
          label: item.label,
          onClick: item.handleClick,
          icon: item.icon,
          color:
            item.isSelectedCallback && item.isSelectedCallback()
              ? ("accent" as IPopoverMenuItemProps["color"])
              : undefined,
        },
        {
          id: "module",
          divider: true,
        },
        ...item.subItems.map((subItem) => ({
          id: subItem.id,
          label: subItem.label,
          icon: subItem.icon,
          onClick: subItem.handleClick,
          color:
            subItem.isSelectedCallback && subItem.isSelectedCallback()
              ? ("accent" as IPopoverMenuItemProps["color"])
              : undefined,
        })),
      ]}
    >
      <div>{button}</div>
    </Popover.Menu>
  ) : (
    button
  );
};

const commonId = "navPrimaryItem";

const NavPrimaryItem = React.memo((props: IMenuPrimaryItemProps) => {
  const { item, showLeadingIcon, handleItemSelect } = props;
  const { label, icon, subItems, id } = item;
  const subItemSelected = useSubItemSelected(item, props.selectedId);
  const { canExpand, isExpanded, toggleExpand } = useExpandItem(
    item,
    subItemSelected,
    props.selectedId
  );

  const handleClick = (e: React.MouseEvent<HTMLLIElement, MouseEvent>) => {
    e.preventDefault();
    e.stopPropagation();
    item.handleClick && item.handleClick(e);
    canExpand && !isExpanded && toggleExpand();
    handleItemSelect(item);
  };

  if (props.isMenuCollapsed) return <CollapsedMenuNavButton {...props} />;
  else
    return (
      <StyledNavPrimaryItem
        {...props}
        onClick={handleClick}
        isExpanded={isExpanded}
        id={commonId + "-" + id}
      >
        <div className={commonId}>
          {showLeadingIcon && (
            <div className="iconWrapper">{icon && <Icon icon={icon} />}</div>
          )}
          <Text
            value={label}
            variant={"h5"}
            color={subItemSelected ? "primary" : undefined}
          />
          {canExpand && (
            <div
              id={commonId + "-" + id + "-collapseButton"}
              onClick={(e) => {
                if (subItemSelected) {
                  e.stopPropagation();
                  e.preventDefault();
                }
              }}
            >
              <Button
                icon={isExpanded ? faChevronDown : faChevronRight}
                spacing="xs"
                outline
                disabled={isExpanded && subItemSelected}
                tooltip={
                  !subItemSelected
                    ? isExpanded
                      ? "Collapse"
                      : "Expand"
                    : undefined
                }
                onClick={(e) => {
                  e?.preventDefault();
                  e?.stopPropagation();
                  toggleExpand();
                }}
              />
            </div>
          )}
        </div>
        {isExpanded && subItems && (
          <ul>
            {subItems.map((subItem) => (
              <SecondaryItem
                key={subItem.id}
                item={subItem}
                handleItemSelect={handleItemSelect}
                showLeadingIcon={showLeadingIcon}
                isSelected={props.selectedId === subItem.id}
              />
            ))}
          </ul>
        )}
      </StyledNavPrimaryItem>
    );
});

NavPrimaryItem.displayName = "NavPrimaryItem";
export { NavPrimaryItem };
export default NavPrimaryItem;
